import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";

export class SquareBannerItems extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
            catId: this.props.catId,
            catTitle: this.props.catTitle,
            withTitle: false
        };
        this.catId = this.props.catId;
        this.catTitle = this.props.catTitle;
        this.withTitle = this.props.withTitle;
        this.createStructure();
    }

    createStructure() {
        this.state.items = this.props.items;
        this.state.isLoaded = true;
        // this.setState({
        //     isLoaded: true,
        //     items: tempItems,
        // });
    }

    componentDidUpdate(prevProps) {
        this.initCr();
    }

    componentDidMount() {
        this.initCr();
    }

    initCr() {
        const ocSQState = {
            responsive: {
                0: {
                    items: 1.5
                },
                320: {
                    items: 1.5
                },
                420: {
                    items: 1.5
                },
                450: {
                    items: 1.5
                },
                550: {
                    items: 1.5
                },
                750: {
                    items: 6
                },
                850: {
                    items: 6
                },
                992: {
                    items: 6
                },
                1200: {
                    items: 6
                }
            }
        };
        var owlCarouselSq = $('.owl-carousel-Sq');
        if (window.innerWidth > 992) {
            owlCarouselSq.owlCarousel({
                responsive: ocSQState.responsive,
                stagePadding: 0,
                rtl: true,
                // loop: true,
                autoplay: false,
                autoWidth: false,
                dots: false,
                // center: true,
                margin: 8,
                // nav: true,
                // navText: ['<i class="fa fa-angle-left" aria-hidden="true"></i>', '<i class="fa fa-angle-right" aria-hidden="true"></i>']
                // navText:["<div class='nav-btn prev-slide'></div>","<div class='nav-btn next-slide'></div>"],

            })
        } else {
            owlCarouselSq.owlCarousel({
                responsive: ocSQState.responsive,
                stagePadding: 0,
                rtl: true,
                loop: true,
                autoplay: false,
                autoWidth: false,
                dots: false,
                center: true,
                margin: 8,
                // nav: true,
                // navText: ['<i class="fa fa-angle-left" aria-hidden="true"></i>', '<i class="fa fa-angle-right" aria-hidden="true"></i>']
                // navText:["<div class='nav-btn prev-slide'></div>","<div class='nav-btn next-slide'></div>"],

            })
        }
    }

    render() {
        const {error, isLoaded, items, catId, catTitle} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            if (this.withTitle) {
                return (
                    <div className="pt-0 pb-3">
                        {/*<div className="mb-3">*/}
                        {/*    <h6 >*/}
                        {/*        {"catTitle"}*/}
                        {/*    </h6>*/}
                        {/*</div>*/}
                        <div className='owl-carousel owl-carousel-Sq'>
                            {
                                items.map((item) => {
                                    return (
                                        <NavLink to={item.onClick.field}>
                                            <div style={{
                                                position: "relative",
                                                width: "100%",
                                                paddingTop: "56.25%" /* 16:9 Aspect Ratio */

                                            }}>
                                                <img
                                                    srcSet={item.src}
                                                    alt={item.title}
                                                    style={{
                                                        position: "absolute",
                                                        top: 0,
                                                        aspectRatio: '16/9',
                                                        width: '100%',
                                                        borderRadius: "10px",
                                                    }}
                                                />
                                            </div>


                                            <div className="single-hero-slide">
                                                <div style={{
                                                    borderRadius: "5px",
                                                    background: "linear-gradient(180deg, rgba(87,87,87,0.2189250700280112) 0%, rgba(0,0,0,0.6979166666666667) 100%)",
                                                    position: "fixed", bottom: 0,
                                                    width: "100%"
                                                }}>
                                                    <h6 className="m-2"
                                                        style={{
                                                            direction: "rtl",
                                                            textAlign: "center",
                                                            textOverflow: "ellipsis",
                                                            whiteSpace: "nowrap",
                                                            overflow: "hidden",
                                                        }}>
                                                        {item.title}
                                                    </h6>
                                                </div>
                                            </div>
                                        </NavLink>
                                    )
                                })
                            }
                        </div>
                    </div>
                );
            } else {
                return (
                    <div className="pt-0 pb-3">
                        {/*<div className="mb-3">*/}
                        {/*    <h6 >*/}
                        {/*        {"catTitle"}*/}
                        {/*    </h6>*/}
                        {/*</div>*/}
                        <div className='owl-carousel owl-carousel-Sq'>
                            {
                                items.map((item) => {
                                    return (
                                        <NavLink to={item.onClick.field}>
                                            <div style={{
                                                position: "relative",
                                                width: "100%",
                                                paddingTop: "56.25%" /* 16:9 Aspect Ratio */

                                            }}>
                                                <img
                                                    srcSet={item.src}
                                                    alt={item.title}
                                                    style={{
                                                        position: "absolute",
                                                        top: 0,
                                                        aspectRatio: '16/9',
                                                        width: '100%',
                                                        borderRadius: "10px",
                                                    }}
                                                />
                                            </div>
                                        </NavLink>
                                    )
                                })
                            }
                        </div>
                    </div>
                );
            }
        }
    }
}
