import React from "react";
import {NavLink} from "react-router-dom";
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import {dynamicItemsHelper, generateRequest} from "../Main";
import {FullscreenSlideShow} from "../ViewComponents/FullscreenSlideShow";
import {BFullscreenSlideShow} from "../ViewComponents/BFullscreenSlideShow";

export class ContentMain extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            sItems: [],
            homeItems: [],
        };

        // this.responsive = {
        //     superLargeDesktop: {
        //         // the naming can be any, depends on you.
        //         breakpoint: {max: 4000, min: 3000},
        //         items: 5
        //     },
        //     desktop: {
        //         breakpoint: {max: 3000, min: 1024},
        //         items: 3
        //     },
        //     tablet: {
        //         breakpoint: {max: 1024, min: 464},
        //         items: 2
        //     },
        //     mobile: {
        //         breakpoint: {max: 464, min: 0},
        //         items: 1
        //     }
        // };
        //

        this.mainSlider = null;
    }

    getStructureItems() {
        generateRequest("https://sepehrapi.irib.ir/v3/check/website_main", 'GET',)
            .then(
                (structureResult) => {
                    this.state.sItems = structureResult;
                    this.createStructure();

                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            )
    }

    createStructure() {
        let sItems = this.state.sItems;
        for (let i = 0; i < sItems.homeStructureRows.length; i++) {
            let sItem = sItems.homeStructureRows[i]
            if (sItem.rowType === "STRUCT_TYPE_SLIDE_SHOW")
                this.mainSlider = sItems.homeStructureRows[i];
            let tempItem = dynamicItemsHelper(sItem)
            this.state.homeItems.push({
                order: i,
                value: tempItem
            });
            this.setState({
                isLoaded: true,
            });

        }
    }


    componentWillReceiveProps(nextProps) {
        // $('#preloaderContainer').show();
        // this.state.isLoaded = false;
        // this.getStructureItems();
    }

    componentDidUpdate(prevProps) {

        // console.log("componentDidUpdate");

        // $('.gallery').flickity({
        // });


    }

    componentDidMount() {
        $('#preloaderContainer').show();
        this.getStructureItems();

        // this.initCr();
        // $('#preloaderContainer').hide();
        // console.log("TIMER START");
        // setTimeout(function () { //Start the timer
        //     this.initCr();
        //     $('#preloaderContainer').hide();
        //     // console.log("TIMER END");
        // }.bind(this), 3200)

    }


    render() {

        const {error, isLoaded, sItems, homeItems} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {
            let homeItemsT = homeItems.sort((a, b) => (a.order > b.order ? 1 : -1));
            let homeItemsR = [];
            for (let i = 0; i < homeItemsT.length; i++) {
                homeItemsR.push(homeItemsT[i].value);
            }


            $('#preloaderContainer').hide();
            if (window.innerWidth > 992) {
                return (
                    <div className="">
                            <FullscreenSlideShow items={this.mainSlider.rowData}/>
                        {/*<div className="container-lg">*/}
                        {/*</div>*/}
                        <div className="container-fluid" style={{marginTop: "-185px"}}>
                            <div className="page-content-wrapper" id="mainContainer">
                                {homeItemsR}
                            </div>
                        </div>
                    </div>
                );
            } else {
                return (
                    <div className="">
                        <div className="container-lg">
                            <div className="page-content-wrapper" id="mainContainer">
                                {homeItemsR}
                            </div>
                        </div>
                    </div>
                );
            }

        }
    }

}