import React from "react";
import {NavLink} from "react-router-dom";

export class FullscreenSlideShow extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
            catId: this.props.catId,
            catTitle: this.props.catTitle
        };
        this.createStructure();
    }

    createStructure() {
        this.state.items = this.props.items;
        this.state.isLoaded = true;
    }

    componentDidMount() {
        this.initCr();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.initCr();
    }

    initCr() {
        $('.owl-carousel-one').owlCarousel({
            items: 1,
            stagePadding: 0,
            rtl: true,
            loop: true,
            autoplay: true,
            dots: false,
            center: true,
            autoWidth: false,
            margin: 0,
            animateIn: 'fadeIn',
            animateOut: 'fadeOut'
        })
    }

    render() {
        const {error, isLoaded, items, catId, catTitle} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            return (
                <div className="">
                    <div className='owl-carousel owl-carousel-one'>
                        {
                            items.data.map((item) => {
                                return (
                                    <NavLink to={item.onClick.field}>
                                        <img className="d-block w-100" src={item.src} alt=""/>
                                        <div style={{
                                            // background:
                                            //     "linear-gradient(rgba(18, 18, 18, 0) 10vw, rgb(18, 18, 18) 46.875vw)," +
                                            //     " linear-gradient(to left, rgba(18, 18, 18, 0.7), rgba(18, 18, 18, 0) 50%)",
                                            background:
                                                "linear-gradient(0deg, rgba(12,21,59,1) 0%, rgba(12,21,59,0.4) 15%, rgba(12,21,59,0.1516981792717087) 100%)",
                                            position: "absolute",
                                            top: 0,
                                            left: 0,
                                            clear: 'float',
                                            width: "100%",
                                            height: "100%",
                                        }}/>
                                        {/*<div className="card single-product-card">*/}
                                        {/*<div className="single-product-card">*/}
                                        {/*    <div className="card-body card-content-live p-0">*/}
                                        {/*<div className="single-hero-slide"*/}
                                        {/*     style={{*/}
                                        {/*         background: "url(" + item.src + ")",*/}
                                        {/*         // WebkitBackgroundSize: "cover",*/}
                                        {/*         // MozBackgroundSize: "cover",*/}
                                        {/*         // OBackgroundSize: "cover",*/}
                                        {/*         backgroundSize: ""*/}
                                        {/*     }}>*/}
                                        {/*</div>*/}
                                        {/*</div>*/}
                                        {/*</div>*/}
                                    </NavLink>
                                )
                            })
                        }
                    </div>
                </div>
            );
        }
    }
}
