export function isLogin() {
    if (sessionStorage.getItem('phoneNumber') != null
        || sessionStorage.getItem('token') != null
        || sessionStorage.getItem('secret') != null)
        return true;
    else
        return false;
}

const CONSUMERKEY = '84ALFkdjpBX0DSR3DsaLo364lKs1hTGq';
const CONSUMERSECRET = 'VPk0dIUxdAPu5NbBAfMKdnC9G99KIKjd';

export class Oauth1Helper {

    static getAuthHeaderForRequest(request, TOKENKEY, TOKENSECRET) {
        const oauth = oauth1a({
            consumer: {key: CONSUMERKEY, secret: CONSUMERSECRET},
            signature_method: 'HMAC-SHA1',
            hash_function(base_string, key) {
                return btoa(CryptoJS.HmacSHA1('sha1', key))
                // .update(base_string)
                // .digest('base64')
            },
        })

        const authorization = oauth.authorize(request, {
            key: TOKENKEY,
            secret: TOKENSECRET,
        });

        return oauth.toHeader(authorization);
    }
}


