export function dynamicItemsHelper(sItem) {
    if (sItem.rowType === "STRUCT_TYPE_SLIDE_SHOW_VIDEO_NEWS") {
        let catId = sItem.rowData.onClick.field;
        let HotNewsCat = sItem.rowData.HotNewsCat;
        let HotNewsCount = sItem.rowData.HotNewsCount;
        let HotNewsTitle = sItem.rowData.HotNewsTitle;
        // let HotNewsTitle = "اخبار ویژه";
        // let HotNewsCat = 1132;
        // let HotNewsCount = 9;
        let HotNewsImg = "img/bg-img/p6.jpg";
        let catTitle = sItem.rowData.title;
        let pageSize = sItem.rowData.count;
        if (window.innerWidth > 992) {
            return <div className="row pt-4">
                <div className="col-8">
                    <VNMedSlideShowItems catId={catId} catTitle={catTitle} pageSize={pageSize}/>
                </div>
                <div className="col-4">
                    <VNMedHotNewsItems HotNewsTitle={HotNewsTitle} HotNewsCat={HotNewsCat} HotNewsCount={HotNewsCount}/>
                </div>
            </div>;
        } else {
            return <div className="row pt-4">
                <VideoNewsSlideShowItems catId={catId} catTitle={catTitle} pageSize={pageSize}/>
                <div className="row p-0 pt-4 m-0">
                    <VNMedHotNewsItems HotNewsTitle={HotNewsTitle} HotNewsCat={HotNewsCat} HotNewsCount={HotNewsCount}/>
                </div>
            </div>;
        }
        return null;
    } else if (sItem.rowType === "STRUCT_TYPE_NEWS_MARQUEE") {
        let catId = sItem.rowData.onClick.field;
        let pageSize = sItem.rowData.count;
        return <VNMarquee catId={catId} pageSize={pageSize}/>
    } else if (sItem.rowType === "STRUCT_TYPE_SLIDE_SHOW") {
        if (window.innerWidth > 992) {
            return null;
        } else {
            return <MainSlideShowItems items={sItem.rowData}/>
        }
        // return <FullscreenSlideShow items={sItem.rowData}/>
    } else if (sItem.rowType === "STRUCT_TYPE_ROW_HORIZONTAL_ITEM_NEWS") {
        if (sItem.rowData.item.type === "TYPE_PLAYING_PROGRAM_HORIZONTAL_NEWS") {
            if (sItem.rowData.item.onLoad.method === "TYPE_SHOW_VIDEO_NEWS") {
                let catId = sItem.rowData.item.onLoad.field;
                let catTitle = sItem.rowData.title;
                let responsive = sItem.rowData.item.responsive;
                // console.log("responsive")
                // console.log(sItem.rowData)
                // console.log(responsive)
                return <VideoNewsCarouselItems catId={catId} catTitle={catTitle} responsive={responsive}/>
            }
        }
    } else if (sItem.rowType === "STRUCT_TYPE_ROW_HORIZONTAL_ITEM") {
        if (sItem.rowData.item.type === "TYPE_PLAYING_PROGRAM_HORIZONTAL") {
            if (sItem.rowData.item.onLoad.method === "TYPE_SHOW_CHANNELS") {
                let catId = sItem.rowData.item.onLoad.field;
                let catTitle = sItem.rowData.title;
                return <LiveCarouselItems catId={catId} catTitle={catTitle} withTitle={true}/>

            }
        }
    } else if (sItem.rowType === "STRUCT_TYPE_SQUARE_BANNER") {
        if (sItem.rowData.type === "TYPE_SQUARE_BANNER") {
            var items = sItem.rowData.data;
            // var catTitle = sItem.rowData.title;
            // return (
            //     <div className="container-lg" style={{direction: "ltr"}}>
            //
            //         <div className='owl-carousel owl-carousel-Sq'>
            //             {
            //                 items.map((item) => {
            //                     return (
            //                         // <div className="item card" >
            //                         <div className="p-1 gallery-cell">
            //                             <NavLink to={item.onClick.field}>
            //                                 <img className="card-img-top" srcSet={item.src}
            //                                      alt={item.title}
            //                                 />
            //
            //                             </NavLink>
            //                         </div>
            //                     )
            //                 })
            //             }
            //         </div>
            //     </div>
            // )

            return <SquareBannerItems items={items} withTitle={sItem.withTitle}/>
            // return null;
        }
    } else if (sItem.rowType === "STRUCT_TYPE_BANNER") {
        return null;
        // return <SimpleBannerItems item={sItem.rowData}/>
    } else if (sItem.rowType === "STRUCT_TYPE_ROW_BANNERS") {
        if (window.innerWidth > 992) {
            return <div className="row">
                {
                    sItem.rowData.map(data => (
                        <div className="col">
                            <SimpleBannerItems item={data}/>
                        </div>
                    ))
                }
            </div>;
        }
        else{
            var items = sItem.rowData;
            return <SquareBannerItems items={items} withTitle={sItem.withTitle}/>
        }

    }
}

