const Main = () => {
    let footer = null;
    if (window.innerWidth > 992) {
        footer = <WebsiteFooter/>;
    }
    return (
        <div className="" style={{position: "relative", paddingBottom: "100px"}}>
            <HeaderArea/>
            <PreLoader/>
            <InternetConnectionStatus/>
            <SidenavBlackOverlay/>
            <SideNavWrapper/>
            <Switch>
                <Route key="Home" exact path='/' component={ContentMain}/>
                {/*<Route exact path={['/live', '/live/:catid']} component={ContentLive} />*/}
                <Route key="PageAboutUs" exact path='/about-us' component={PageAboutUs}/>
                <Route key="PageRights" exact path='/rights' component={PageRights}/>
                <Route key="DownloadApp" exact path='/app' component={DownloadApps}/>
                <Route key="LoginMain" exact path='/login' component={LoginMain}/>
                <Route key="LogoutMain" exact path='/logout' component={LogoutMain}/>
                <Route key="ContentLive" exact path='/live' component={ContentLive}/>
                <Route key="ContentLiveByCat" exact path='/live/:catId' component={ContentLive}/>
                <Route key="ContentLivePlay" exact path='/live/play/:channelid' component={ContentLivePlay}/>
                <Route key="ContentLivePlayEPG" exact path='/live/play/:channelid/:programid'
                       component={ContentLivePlay}/>
                {/*<Route exact path='/vod' component={ComingSoonPage}/>*/}
                <Route key="ContentVideoNews" exact path='/videonews' component={ContentMainNews}/>
                <Route key="ContentVideoNewsPlay" exact path='/videonews/play/:vncid' component={ContentVideoNewsPlay}/>
                <Route key="ContentVideoNewsPlayByCatId" exact path='/videonews/play/:vncid/:vnid'
                       component={ContentVideoNewsPlay}/>
                <Route key="ContentVideoNewsPlayT" exact path='/videonews/playt/:vncid'
                       component={ContentVideoNewsPlay2}/>
                <Route key="ContentVideoNewsPlayByCatIdT" exact path='/videonews/playt/:vncid/:vnid'
                       component={ContentVideoNewsPlay2}/>
                <Route key="NotFound" component={NotFoundPage}/>
            </Switch>
            {footer}
            <FooterNav/>
        </div>
    );
}

export {Main};
