import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";

export class VNFullscreenSlideShow extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
            catId: this.props.catId,
            catTitle: this.props.catTitle,
            pageSize: this.props.pageSize
        };
        this.catId = this.props.catId;
        this.catTitle = this.props.catTitle;
        this.pageSize = this.props.pageSize;
        this.createStructure();
    }

    createStructure() {

        generateRequest(
            "https://sepehrapi.irib.ir/v3/videonews?category_id=" + this.catId
            + "&page_size=" + this.pageSize
            + "&page=1&include_media_resources=true&include_details=true&order_types=RELEASE_DATE_DESC", 'GET')
            .then(
                (sliderResult) => {
                    this.setState({
                        isLoaded: true,
                        items: sliderResult
                    });
                },
                (error) => {
                    return null;
                }
            )
    }

    componentDidMount() {
        this.initCr();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.initCr();
    }

    initCr() {
        $('.owl-carousel-one').owlCarousel({
            items: 1,
            stagePadding: 0,
            rtl: true,
            loop: true,
            autoplay: true,
            dots: false,
            center: true,
            autoWidth: false,
            margin: 0,
            animateIn: 'fadeIn',
            animateOut: 'fadeOut'
        })
    }

    render() {
        const {error, isLoaded, items} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            return (
                <div className="">
                    <div className='owl-carousel owl-carousel-one'>
                        {
                            items.list.map((item) => {
                                return (
                                    <NavLink to={"/videonews/play/" + this.catId + "/" + item.id} className="">

                                        <div className="single-hero-slide"
                                             style={{'background-image': 'url("' + item.cover + '")', height: "800px"}}>
                                            <div style={{
                                                // background:
                                                //     "linear-gradient(rgba(18, 18, 18, 0) 10vw, rgb(18, 18, 18) 46.875vw)," +
                                                //     " linear-gradient(to left, rgba(18, 18, 18, 0.7), rgba(18, 18, 18, 0) 50%)",
                                                background:
                                                    "linear-gradient(0deg, rgba(12,21,59,1) 0%, rgba(12,21,59,0.4) 15%, rgba(12,21,59,0.1516981792717087) 100%)",
                                                position: "absolute",
                                                top: 0,
                                                left: 0,
                                                clear: 'float',
                                                width: "100%",
                                                height: "100%",
                                            }}/>
                                            <div className="slide-content h-100 d-flex align-items-center text-right">
                                                <div className="container-fluid mx-3">
                                                    <h3 className="text-white" data-animation="fadeInUp"
                                                        data-delay="100ms" data-wow-duration="500ms">{item.title}</h3>
                                                    {/*<p className="text-white mb-4" data-animation="fadeInUp"*/}
                                                    {/*   data-delay="400ms" data-wow-duration="500ms"></p>*/}
                                                    {/*<a className="btn btn-creative btn-warning" href="#"*/}
                                                    {/*data-animation="fadeInUp" data-delay="800ms"*/}
                                                    {/*data-wow-duration="500ms">Purchase Now</a>*/}
                                                </div>
                                            </div>
                                        </div>
                                        {/*                                        <img className="d-block w-100" src={item.cover} alt="" style={{height: "800px"}}/>*/}
                                        {/*s*/}


                                        {/*<p className="position-absolute top-50 start-0 translate-middle">{item.title}</p>*/}
                                        {/*<div className="card single-product-card">*/}
                                        {/*<div className="single-product-card">*/}
                                        {/*    <div className="card-body card-content-live p-0">*/}
                                        {/*<div className="single-hero-slide"*/}
                                        {/*     style={{*/}
                                        {/*         background: "url(" + item.src + ")",*/}
                                        {/*         // WebkitBackgroundSize: "cover",*/}
                                        {/*         // MozBackgroundSize: "cover",*/}
                                        {/*         // OBackgroundSize: "cover",*/}
                                        {/*         backgroundSize: ""*/}
                                        {/*     }}>*/}
                                        {/*</div>*/}
                                        {/*</div>*/}
                                        {/*</div>*/}
                                    </NavLink>
                                )
                            })
                        }
                    </div>
                </div>
            )

            return (
                <div className="">
                    {/*<h6 className="lead">*/}
                    {/*    {this.catTitle}*/}
                    {/*</h6>*/}
                    <div className='owl-carousel-one owl-carousel'>
                        {
                            items.list.map((item) => {
                                return (
                                    <div className="single-hero-slide item">
                                        <NavLink to={"/videonews/play/" + this.catId + "/" + item.id}>
                                            <img style={{
                                                height: "100%",
                                                width: "100%",
                                                objectFit: "fill"
                                            }}
                                                 src={item.cover}/>
                                            <div style={{
                                                // background: rgb(87,87,87);
                                                background: "linear-gradient(180deg, rgba(87,87,87,0.2189250700280112) 0%, rgba(0,0,0,0.6979166666666667) 100%)",
                                                position: "absolute", bottom: 0,
                                                width: "100%"
                                            }}>
                                                <h6 className="lead"
                                                    style={{
                                                        direction: "rtl",
                                                        textAlign: "center",
                                                        textOverflow: "ellipsis",
                                                        whiteSpace: "nowrap",
                                                        overflow: "hidden",
                                                    }}>
                                                    {item.title}
                                                </h6>
                                            </div>
                                        </NavLink>
                                    </div>

                                )
                            })
                        }
                    </div>
                </div>
            )

        }
    }
}
