import React, {useEffect, useState} from "react";


let deferredPrompt;
let haveWebApp = false;
const DownloadApps = () => {
    const [installable, setInstallable] = useState(false);

    useEffect(() => {
        window.addEventListener("beforeinstallprompt", (e) => {
            // Prevent the mini-infobar from appearing on mobile
            e.preventDefault();
            // Stash the event so it can be triggered later.
            deferredPrompt = e;
            // Update UI notify the user they can install the PWA
            setInstallable(true);
        });

        window.addEventListener('appinstalled', () => {
            // Log install to analytics
            console.log('INSTALL: Success');
        });
    }, []);

    const handleInstallClick = (e) => {
        // Hide the app provided install promotion
        setInstallable(false);
        // Show the install prompt
        deferredPrompt.prompt();
        // Wait for the user to respond to the prompt
        deferredPrompt.userChoice.then((choiceResult) => {
            if (choiceResult.outcome === 'accepted') {
                console.log('User accepted the install prompt');
            } else {
                console.log('User dismissed the install prompt');
            }
        });
    };
    window.addEventListener('appinstalled', () => {
        // Hide the app-provided install promotion
        hideInstallPromotion();
        // Clear the deferredPrompt so it can be garbage collected
        deferredPrompt = null;
        haveWebApp = true;
        // Optionally, send analytics event to indicate successful install
        console.log('PWA was installed');
    });

    $('#preloaderContainer').hide();
    return (
        <div>
            <div className="container"
                 style={{textAlign: "center", alignContent: "center", paddingTop: "80px"}}>

                <h2>اپلیکیشن اندروید و تبلت</h2>
                <div className="row">
                    <div className="col-sm-6" style={{paddingTop: "12px"}}>
                        <div className="card" style={{display: "block"}}>
                            <img className="card-img-top"
                                 src="/dap/androidA.png"
                                 alt="Card image" style={{width: "80%"}} align="middle"/>
                            <div className="card-body">

                                <h4 className="card-title">دانلود اپلیکیشن اندروید
                                    <img src="/dap/android.svg" alt=""
                                         style={{
                                             filter: "invert(100%) sepia(100%) saturate(0%) hue-rotate(125deg) brightness(103%) contrast(103%)"
                                         }}/>
                                </h4>
                                <a href="./uploads/files/android/Sepehr_VideoNews_v175.apk"
                                   className="btn bg-light text-dark" style={{width: "80%"}}>
                                    <img src="/dap/direct-download.png"
                                         style={{height: "25px"}}/>
                                    <p style={{fontSize: "16px", fontWeight: "bold"}}>لینک مستقیم</p>
                                </a>
                                <a href="https://cafebazaar.ir/app/ir.IRIB.jk.MediaIRIB"
                                   className="btn bg-light text-dark"
                                   style={{width: "80%", marginTop: "8px"}}>
                                    <img src="/dap/bazar.png"
                                         style={{height: "25px"}}/>
                                    <p style={{fontSize: "16px", fontWeight: "bold"}}>کافه بازار</p>
                                </a>
                                <a href="https://myket.ir/app/ir.IRIB.jk.MediaIRIB"
                                   className="btn bg-light text-dark"
                                   style={{width: "80%", marginTop: "8px"}}>
                                    <img src="/dap/myket_logo.png"
                                         style={{height: "25px"}}/>
                                    <p style={{fontSize: "16px", fontWeight: "bold"}}>مایکت</p>
                                </a>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm-6" style={{paddingTop: "12px"}}>
                        <div className="card" style={{display: "block"}}>
                            <img className="card-img-top"
                                 src="/dap/iosA.png"
                                 alt="Card image" style={{width: "91%"}} align="middle"/>
                            <div className="card-body">

                                <h4 className="card-title">دانلود اپلیکیشن iOS
                                    <img src="/dap/apple.svg" alt=""
                                         style={{
                                             filter: "invert(100%) sepia(100%) saturate(0%) hue-rotate(125deg) brightness(103%) contrast(103%)"
                                         }}/>
                                </h4>
                                <div style={{paddingTop: "80px"}}></div>
                                <a
                                    className="btn bg-light text-dark" style={{width: "80%"}}
                                    data-bs-toggle="modal"
                                    data-bs-target="#shareModal">
                                    <p style={{fontSize: "16px", fontWeight: "bold"}}>وب اپلیکیشن</p>
                                    {/*<img width={40} height={40} src="/img/icons/share.svg"/>*/}
                                    <img src="/dap/logo.png"
                                         style={{height: "25px"}}/>
                                </a>
                                <div style={{paddingTop: "90px"}}></div>

                            </div>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col-2"></div>
                    <div className="col-sm-8" style={{paddingTop: "12px"}}>
                        <div className="card" style={{display: "block"}}>
                            <img className="card-img-top"
                                 src="/dap/win-log.png"
                                 alt="Card image" style={{width: "50%", marginTop: "20px"}} align="middle"/>
                            <div className="card-body"/>

                            <h4 className="card-title">
                                دانلود اپلیکیشن ویندوز
                            </h4>
                            {installable &&
                            <button className="btn  btn-danger text-dark" onClick={handleInstallClick}
                                    style={{width: "50%", marginBottom: "20px"}}>
                                نصب اپلیکیشن
                            </button>
                            }
                            {!installable &&
                            <button className="btn  btn-danger text-dark"
                                    style={{width: "50%", marginBottom: "20px"}}>
                                 اپلیکیشن برای شما قبلا نصب شده است و یا برای مرورگر شما امکان پذیر نیست.
                                {/*{haveWebApp &&*/}
                                {/*<p></p>*/}
                                {/*}*/}
                            </button>
                            }
                            {/*<p style={{fontSize: "16px", fontWeight: "bold"}}>به زودی</p>*/}
                        </div>
                        <div style={{paddingBottom: "20px"}}></div>
                    </div>
                </div>
                <div style={{height: "100px"}}></div>
            </div>

            <div className="modalContainer">
                <div className="modal fade" id="shareModal" tabIndex="-1"
                     aria-labelledby="shareModalLabel" aria-hidden="true">
                    <div
                        className="modal-dialog modal-dialog-centered modal-dialog-scrollable">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h6 className="modal-title"
                                    id="shareModalLabel">نصب اپلیکیشن</h6>
                            </div>
                            <div className="modal-body" id="shareModalBody">
                                <div className="container appDialogIOS" id="appDialogIOS"
                                     style={{direction: "rtl"}}>

                                    <div className="row">
                                        <div className="col-md-2"></div>
                                        <div className="col-md-8">
                                            <div className="row space-16"></div>
                                            <div className="row">
                                                <div className="col-sm-12">
                                                    <div className="thumbnail">
                                                        <div className="caption text-center">
                                                            <div className="position-relative">
                                                                <img src="/dap/logo.png"
                                                                     style={{width: "72px", height: "72px"}}/>
                                                            </div>
                                                            <h4 id="thumbnail-label">وب اپلیکیشن سپهر را به صفحه اصلی
                                                                موبایل خود اضافه
                                                                کنید.</h4>
                                                            <div className="thumbnail-description">
                                                                برای نصب اپلیکیشن سپهر
                                                                را روی سایت <b>مرورگرسافاری</b> باز کنید.

                                                                {/*<a href="/">سپهر</a>*/}

                                                            </div>
                                                            <div className="thumbnail-description">1- در نوار پایین دکمه
                                                                <img src="/dap/icon-share.png"
                                                                     style={{
                                                                         width: "20px", height: "30px",
                                                                         marginRight: "8px", marginLeft: "9px"
                                                                     }}/>
                                                                را انتخاب کنید.
                                                            </div>

                                                            <div className="thumbnail-description">2- منوی بازشده را به
                                                                چپ بکشید و گزینه
                                                                <br/>
                                                                <b> "Add to home screen" </b>
                                                                را انتخاب کنید
                                                            </div>

                                                            <img src="/dap/add-to-home-screen.png"
                                                                 alt="" className="img-responsive"/>

                                                            <div className="thumbnail-description"></div>
                                                            <div className="thumbnail-description"> 3- در مرحله بعد در
                                                                قسمت بالا روی
                                                                <b>"Add"</b>
                                                                یا
                                                                <b>"save"</b>
                                                                کلیک کنید
                                                            </div>

                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="col-md-2">&nbsp;</div>
                                        </div>
                                    </div>

                                </div>

                            </div>
                            <div className="modal-footer">
                                <button
                                    className="btn btn-sm btn-secondary center w-100 m-auto"
                                    type="button"
                                    data-bs-dismiss="modal">
                                    بستن
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
export
{
    DownloadApps
};

