import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";
import ReactHlsPlayer from "react-hls-player";
// import VrPlayer from "react-vr-player";
// import ReactPlayer from "react-player";


export class LivePlayer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false
        };


        this.programTitle = this.props.programTitle !== undefined ? this.props.programTitle : "";
        this.mediaUrl = this.props.mediaUrl !== undefined ? this.props.mediaUrl : "";
        this.nowEpgItem = this.props.nowEpgItem !== undefined ? this.props.nowEpgItem : [];
        this.chItem = this.props.chItem !== undefined ? this.props.chItem : [];
        this.createStructure();

        this.playerRef = React.createRef();

        this.channelName = this.chItem.name;
        this.programName = this.programTitle;
    }

    createStructure() {
        // var sortOrder = this.playerRef.tester.getAttribute("data-sortorder");

        sessionFactory.init("");
        activityFactory.log(this.channelName, this.programName, 1, 1, 7, 0);
        this.state.isLoaded = true;
    }


    componentDidMount() {
        // this.playerRef.current.channelName = this.channelName;
        // this.playerRef.current.programName = this.programName;
        this.playerRef.current.addEventListener('play', this.fireOnVideoStart);
        this.playerRef.current.addEventListener('pause', this.fireOnVideoPause);
        this.playerRef.current.addEventListener('ended', this.fireOnVideoEnd);
    }


    fireOnVideoStart(evt) {
        // console.log("PLAY")
        // console.log(this.channelName)
        activityFactory.log(evt.currentTarget.getAttribute("data-chname"), evt.currentTarget.getAttribute("data-prname"), 1, 1, 1, 0);
        // this.isPlay = !this.isPlay;

    }

    fireOnVideoPause(evt) {
        // console.log("PAUSE")
        activityFactory.log(evt.currentTarget.getAttribute("data-chname"), evt.currentTarget.getAttribute("data-prname"), 1, 1, 2, 0);

    }

    fireOnVideoEnd(evt) {
        // console.log("END")
        activityFactory.log(evt.currentTarget.getAttribute("data-chname"), evt.currentTarget.getAttribute("data-prname"), 1, 1, 3, 0);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
    }

    formatDate(unix_timestamp) {
        var date = new Date(unix_timestamp);
        var hours = "0" + date.getHours();
        var minutes = "0" + date.getMinutes();
        var seconds = "0" + date.getSeconds();
        var formattedTime = hours.substr(-2) + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);
        return formattedTime;

    }

    render() {

        const {error, isLoaded} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            return (
                <div className="card product-details-card mb-1">
                    <div className="card product-details-card">
                        <div className="card-body p-0">
                            <img className="p-1"
                                 style={{
                                     height: "50px",
                                     // width: "100px",
                                     objectFit: "fill",
                                     float: "right",
                                     marginRight: "12px"
                                 }}
                                 src={this.chItem.poster}/>
                            <h5 id="curProgramTitle"
                                style={{
                                    // fontSize: "90%",
                                    // maxWidth: "300px",
                                    textOverflow: "ellipsis",
                                    whiteSpace: "nowrap",
                                    overflow: "hidden",
                                    padding: "10px"
                                }}
                                className="">
                                { this.chItem.name !== undefined ? "پخش زنده شبکه " + this.chItem.name : "بازپخش"}
                            </h5>
                        </div>
                    </div>
                    <div>

                        <div className="card-body p-0" id="playerBody">
                            {/*<VrPlayer*/}
                            {/*    sources={sources}*/}
                            {/*    brand="Some Brand Name"*/}
                            {/*    title="Some Video Title"*/}
                            {/*    keys={keys} />*/}

                            {/*<ReactPlayer url={this.mediaUrl} />*/}
                            <ReactHlsPlayer
                                data-chName={this.channelName}
                                data-prName={this.programName}
                                playerRef={this.playerRef}
                                src={this.mediaUrl}
                                autoPlay={false}
                                controls={true}
                                width="100%"
                                height="auto"
                            />
                        </div>
                    </div>
                    <div className="card product-details-card">
                        <div className="card-body p-0">

                            <div className="accordion accordion-style-one" id="accordionStyle1">
                                <div className="accordion-item">
                                    <div className="accordion-header"
                                        // onClick={this.loadDetails(nowEpgItem.id)}
                                         id={"accordionEPG"}>
                                        <h6 className="collapsed" data-bs-toggle="collapse"
                                            data-bs-target="#accordionStyleEPG"
                                            aria-expanded="false"
                                            aria-controls="accordionStyleEPG">
                                            {this.nowEpgItem.title + " - " + this.formatDate(this.nowEpgItem.start)}
                                            <i className="fa fa-angle-down"/>
                                        </h6>
                                    </div>

                                    <div className="accordion-collapse collapse"
                                         id={"accordionStyleEPG"}
                                         aria-labelledby={"accordion"}
                                         data-bs-parent="#accordionStyle1">
                                        <div className="accordion-body">
                                            <div className="mb-0"
                                                 id={"curProgramDesc" + this.nowEpgItem.id}>
                                                {this.nowEpgItem.descSummary}
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            );
        }
    }
}
