import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";
import ReactHlsPlayer from "react-hls-player";
import ReactDOM from "react-dom";

export class VideoNewsPlayItem2 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            vnItem: null,
            catId: null,
        };
        this.vnItem = this.props.vnItem;
        this.catId = this.props.catId;
        // this.createStructure();
    }

    componentDidMount() {
        // this.state.isLoaded = true;
        this.setState({
            isLoaded: true,
        });
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
    }


    playNews(vnItemT, handlePlayer) {
        let viewerHtml = "";
        if (vnItemT.media_resources.mediaResources[0].src.includes(".m3u8")) {
            console.log("HLS")
            viewerHtml =
                <ReactHlsPlayer
                    src={vnItemT.media_resources.mediaResources[0].src}
                    autoPlay={false}
                    controls={true}
                    width="100%"
                    height="auto"
                />
        } else {
            console.log("MP4")
            viewerHtml =
                <video
                    autoPlay={false}
                    width="100%"
                    height="auto"
                    controls={true}>
                    <source src={vnItemT.media_resources.mediaResources[0].src} type="video/mp4"/>
                    Your browser does not support the video tag.
                </video>
        }
        ReactDOM.render(viewerHtml, document.getElementById("vnItemT_" + vnItemT.id));
        handlePlayer();
    }

    loadDetails(vnid) {
        {
            generateRequest("https://sepehrapi.irib.ir/v3/videonews/?item_id_list=" +
                vnid + "&include_media_resources=true&include_details=true", 'GET')
                .then(
                    (vnResult) => {
                        let vnItem = vnResult.list[0];
                        let itemDesc = "  ";
                        if (vnItem.details != null) {
                            itemDesc = vnItem.details.description != null ? vnItem.details.description : "  ";
                        }
                        $('#videoNewsDesc' + vnid).html(itemDesc);
                    },
                    (error) => {
                        this.setState({
                            isLoaded: true,
                            error
                        });
                    }
                )
        }
    }


    render() {

        const {error, isLoaded, vnItem} = this.state;
        const {handlePlayer} = this.props;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {
            let viewerHtml = "";
            let vnItemT = this.vnItem;
            let playIcon = null;
            vnItemT.poster = vnItemT.poster == undefined ? "/img/no-image.png" : vnItemT.poster;
            if (vnItemT.media_resources.mediaResources !== null) {
                viewerHtml =
                    <div id={"vnItemT_" + vnItemT.id} className="single-image-gallery">
                        <div
                            onClick={() => this.playNews(vnItemT, handlePlayer)}
                            // onChange={() => handlePlayer}
                            style={{
                                width: "100%",
                                height: "100%",
                                zIndex: 1,
                                position: "absolute",
                                cursor: "pointer"
                                // right: "43%",
                                // background: "red"
                            }}>

                        </div>
                        <span className="gallery-img2" href={vnItemT.poster} data-effect="mfp-zoom-in"
                              style={{
                                  position: "relative",
                                  top: 0,
                                  left: 0,
                              }}>
                            <div style={{
                                position: "relative",
                                width: "100%",
                                paddingTop: "56.25%" /* 16:9 Aspect Ratio */

                            }}>
                            {/*<object data="/img/icons/noimage.svg" type="image/png">*/}
                                <img
                                    src={vnItemT.poster == undefined ? "/img/no-image.png" : vnItemT.poster}
                                    alt={vnItemT.title}
                                    style={{
                                        position: "absolute",
                                        top: 0,
                                        aspectRatio: '16/9',
                                        width: '100%',
                                    }}/>
                                <img
                                    src="/img/playerHover.png"
                                    alt={vnItemT.title}
                                    style={{
                                        position: "absolute",
                                        top: 0,
                                        aspectRatio: '16/9',
                                        width: '100%',
                                    }}/>

                                {/*</object>*/}
                        </div>

                        </span>


                        {/*<span className="fav-icon shadow" onClick={() => this.favIconClick()}>*/}
                        {/*                <svg width="16" height="16" viewBox="0 0 16 16" className="bi bi-heart-fill"*/}
                        {/*                     fill="currentColor" xmlns="http://www.w3.org/2000/svg">*/}
                        {/*                    <path fillRule="evenodd"*/}
                        {/*                          d="M8 1.314C12.438-3.248 23.534 4.735 8 15-7.534 4.736 3.562-3.248 8 1.314z"/>*/}
                        {/*                </svg>*/}
                        {/*            </span>*/}
                    </div>
            } else {

                viewerHtml =
                    <div id={"vnItemT_" + vnItemT.id} className="single-image-gallery">
                        {playIcon}
                        <span className="gallery-img2" href={vnItemT.poster} data-effect="mfp-zoom-in">

                            <img
                                src={vnItemT.poster}
                                alt={vnItemT.title}
                                style={{
                                    opacity: 1,
                                    width: '100%',
                                    aspectRatio: '16/9',
                                    borderRadius: "10px",
                                }}/>

                        </span>


                        {/*<span className="fav-icon shadow" onClick={() => this.favIconClick()}>*/}
                        {/*                <svg width="16" height="16" viewBox="0 0 16 16" className="bi bi-heart-fill"*/}
                        {/*                     fill="currentColor" xmlns="http://www.w3.org/2000/svg">*/}
                        {/*                    <path fillRule="evenodd"*/}
                        {/*                          d="M8 1.314C12.438-3.248 23.534 4.735 8 15-7.534 4.736 3.562-3.248 8 1.314z"/>*/}
                        {/*                </svg>*/}
                        {/*            </span>*/}
                    </div>

            }

            let itemDesc = "  ";
            if (vnItemT.details != null) {
                itemDesc = vnItemT.details.description != null ? vnItemT.details.description : "  ";
            }
            return (
                <div className="page-content-wrapper" style={{zIndex: "unset"}}>

                    <div className="product-details-card mb-3">
                        <h6 style={{color: "rgb(255,80,118)"}}>
                            {vnItemT.title + " - " + vnItemT.releaseDate + " (" + vnItemT.provider.title + ") "}
                        </h6>

                        <div className="p-1">

                            <div className="accordion accordion-style-one" id="accordionStyle1">
                                <div className="">

                                    <div className="" id={"accordion" + vnItemT.id}
                                         onClick={this.loadDetails(vnItemT.id)}>
                                        {/*onClick={() => this.loadDetails(vnItemT.id)}>*/}
                                    </div>
                                    <div
                                        className="vnDetailContainer"
                                         id={"accordionStyle" + vnItemT.id}
                                         aria-labelledby={"accordion" + vnItemT.id}
                                         data-bs-parent="#accordionStyle1">
                                        <div className="accordion-body">
                                            <div className="mb-0 text-light" id={"videoNewsDesc" + vnItemT.id}>
                                                در حال دریافت اطلاعات...
                                            </div>
                                        </div>
                                    </div>


                                </div>
                            </div>
                        </div>

                        <div className="image-gallery-card">
                            {viewerHtml}
                        </div>
                    </div>

                </div>
            );
        }
    }
}
