import React from "react";
import {NavLink} from "react-router-dom";
import {dynamicItemsHelper, generateRequest} from "../Main";
import {FullscreenSlideShow} from "../ViewComponents/FullscreenSlideShow";
import {VNFullscreenSlideShow} from "../ViewComponents/VNFullscreenSlideShow";

export class ContentMainNews extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            sItems: [],
            homeItems: []
        };
        this.sliderCatId = null;
        this.sliderCatTitle = null;
        this.sliderPageSize = null;
    }

    getStructureItems() {
        generateRequest("https://sepehrapi.irib.ir/v3/check/website_news", 'GET')
            .then(
                (structureResult) => {
                    this.state.sItems = structureResult;
                    this.createStructure();

                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            )
    }

    createStructure() {
        let sItems = this.state.sItems;
        for (let i = 0; i < sItems.homeStructureRows.length; i++) {
            let sItem = sItems.homeStructureRows[i]
            // if (sItem.rowType === "STRUCT_TYPE_SLIDE_SHOW_VIDEO_NEWS") {
            //     this.sliderCatId = sItem.rowData.onClick.field;
            //     this.sliderCatTitle = sItem.rowData.title;
            //     this.sliderPageSize = sItem.rowData.count;
            // }
            let tempItem = dynamicItemsHelper(sItem);
            this.state.homeItems.push({
                order: i,
                value: tempItem
            });
            this.setState(
                {
                    isLoaded: true,
                }
            );
        }

    }

    componentWillReceiveProps(nextProps) {
        // $('#preloaderContainer').show();
        // this.state.isLoaded = false;
        // this.getStructureItems();
    }

    componentDidUpdate(prevProps) {
        const ocSQState = {
            responsive: {
                0: {
                    items: 2
                },
                320: {
                    items: 3
                },
                420: {
                    items: 3
                },
                450: {
                    items: 3
                },
                550: {
                    items: 3
                },
                750: {
                    items: 5
                },
                850: {
                    items: 5
                },
                992: {
                    items: 5
                },
                1200: {
                    items: 5
                }
            }
        };

        var owlCarouselSq = $('.owl-carousel-Sq');
        owlCarouselSq.owlCarousel({
            responsive: ocSQState.responsive,
            stagePadding: 0,
            rtl: true,
            loop: true,
            autoplay: false,
            autoWidth: false,
            dots: false,
            center: true,
            margin: 8,
            // nav: true,
            // navText: ['<i class="fa fa-angle-left" aria-hidden="true"></i>', '<i class="fa fa-angle-right" aria-hidden="true"></i>']
            // navText:["<div class='nav-btn prev-slide'></div>","<div class='nav-btn next-slide'></div>"],

        })

        const ocState = {
            responsive: {
                0: {
                    items: 1.5
                },
                320: {
                    items: 1.5
                },
                420: {
                    items: 1.5
                },
                450: {
                    items: 1.5
                },
                550: {
                    items: 1.5
                },
                750: {
                    items: 3.5
                },
                850: {
                    items: 3.5
                },
                992: {
                    items: 3.5
                },
                1200: {
                    items: 3.5
                }
            }
        };

        $('.owl-carousel-one').owlCarousel({
            items: 1,
            stagePadding: 0,
            rtl: true,
            loop: true,
            autoplay: true,
            autoWidth: false,
            dots: true,
            center: true,
            margin: 8,
            // nav: true,
            // navText : ["<i class='fa fa-chevron-left'></i>","<i class='fa fa-chevron-right'></i>"],
            // navText: [('<i class="fa fa-angle-right"></i>'), ('<i class="fa fa-angle-left"></i>')],
            animateIn: 'fadeIn',
            animateOut: 'fadeOut'
        })
        $('.owl-carousel-NS').owlCarousel({
            responsive: ocState.responsive,
            stagePadding: 0,
            rtl: true,
            loop: true,
            autoplay: true,
            autoWidth: false,
            dots: true,
            center: false,
            margin: 8,
            animateIn: 'fadeIn',
            animateOut: 'fadeOut'
        })


    }

    componentDidMount() {
        $('#preloaderContainer').show();
        this.getStructureItems();
    }


    render() {

        const {error, isLoaded, sItems, homeItems} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            let homeItemsT = homeItems.sort((a, b) => (a.order > b.order ? 1 : -1));
            let homeItemsR = [];
            for (var i = 0; i < homeItemsT.length; i++) {
                homeItemsR.push(homeItemsT[i].value);
            }

            $('#preloaderContainer').hide();
            // if (window.innerWidth > 992) {
            //     return (
            //         <div className="">
            //             <VNFullscreenSlideShow catId={this.sliderCatId}
            //                                    catTitle={this.sliderCatTitle}
            //                                    pageSize={this.sliderPageSize}/>
            //             {/*<div className="container-lg">*/}
            //             {/*</div>*/}
            //             <div className="container-fluid" style={{marginTop: "-185px"}}>
            //                 <div className="page-content-wrapper" id="mainContainer">
            //                     {homeItemsR}
            //                 </div>
            //             </div>
            //         </div>
            //     );
            // } else {
            return (
                <div className="container-fluid">
                    <div className="page-content-wrapper" id="mainContainer">
                        {homeItemsR}
                    </div>
                </div>
            );
            // }
        }
    }
}
