import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";

export class MainSlideShowItems extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
            catId: this.props.catId,
            catTitle: this.props.catTitle
        };
        this.createStructure();
    }

    createStructure() {
        this.state.items = this.props.items;
        this.state.isLoaded = true;
    }

    componentDidMount() {
        this.initCr();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.initCr();
    }

    initCr() {
        $('.owl-carousel-one').owlCarousel({
            items: 1,
            stagePadding: 0,
            rtl: true,
            loop: true,
            autoplay: true,
            dots: true,
            center: true,
            autoWidth: false,
            margin: 8,
            animateIn: 'fadeIn',
            animateOut: 'fadeOut'
        })
    }

    render() {
        const {error, isLoaded, items, catId, catTitle} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            return (
                <div className="">
                    <div className='owl-carousel owl-carousel-one'>
                        {
                            items.data.map((item) => {
                                return (
                                    <NavLink to={item.onClick.field}>
                                        {/*<div className="card single-product-card">*/}
                                        <div className="single-product-card">
                                            <div className="card-body card-content-live p-0">
                                                <div className="single-hero-slide">
                                                    <img style={{
                                                        height: "100%",
                                                        width: "100%",
                                                        objectFit: "fill"
                                                    }}
                                                         src={item.src}/>
                                                </div>
                                            </div>
                                        </div>
                                    </NavLink>
                                )
                            })
                        }
                    </div>
                </div>
            );
        }
    }
}
