import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";
import ReactHlsPlayer from "react-hls-player";
import {ContentLiveSTName2ID} from "../Utils/Helper";
import MetaTags from 'react-meta-tags';

import '../Utils/didban'

let chItemTemp;

export class LiveFramePlayer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            chItem: []
        };

        this.playerRef = React.createRef();

        this.createStructure();
        this.isPlay = false;
        this.channelName = "";
        this.programName = "";
    }


    createStructure() {
        const targetStreamName = this.props.match.params.streamname == null ? "" : this.props.match.params.streamname;
        generateRequest("https://sepehrapi.irib.ir/v3/channels/?key=" + targetStreamName
            + "&include_media_resources=true&include_details=true", 'GET')
            .then(
                (chResult) => {
                    this.channelName = chResult.list[0].name
                    this.programName = chResult.list[0].currentProgram.title
                    // console.log(this.channelName)
                    // console.log(this.programName)
                    chItemTemp = chResult.list[0];
                    sessionFactory.init("");
                    // console.log(this.channelName)
                    // console.log(this.programName)
                    activityFactory.log(this.channelName, this.programName, 1, 1, 7, 0);
                    this.setState({
                        isLoaded: true,
                        chItem: chResult.list[0],
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            )
        // this.state.isLoaded = true;
    }


    componentDidMount() {
        // $('[data-theme="dark"] body').css("background-color","transparent");
        // $('[data-theme="dark"]').css("background-color","transparent");
        // document.body.style.backgroundColor = "transparent";
        // document.html.style.backgroundColor = "transparet";

    }


    fireOnVideoStart() {
        this.channelName = chItemTemp.name
        this.programName = chItemTemp.currentProgram.title
        // console.log("PLAY")
        activityFactory.log(this.channelName, this.programName, 1, 1, 1, 0);
        // this.isPlay = !this.isPlay;

    }

    fireOnVideoPause() {
        this.channelName = chItemTemp.name
        this.programName = chItemTemp.currentProgram.title
        // console.log("PAUSE")
        activityFactory.log(this.channelName, this.programName, 1, 1, 2, 0);

    }

    fireOnVideoEnd() {
        console.log("END")
        activityFactory.log(this.channelName, this.programName, 1, 1, 3, 0);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        // $('[data-theme="dark"] body').css("background-color","transparent");
        // $('[data-theme="dark"]').css("background-color","transparent");
        if (this.playerRef != null && this.playerRef.current != null) {
            this.playerRef.current.addEventListener('play', this.fireOnVideoStart);
            this.playerRef.current.addEventListener('pause', this.fireOnVideoPause);
            this.playerRef.current.addEventListener('ended', this.fireOnVideoEnd);
        }
    }

    formatDate(unix_timestamp) {
        var date = new Date(unix_timestamp);
        var hours = "0" + date.getHours();
        var minutes = "0" + date.getMinutes();
        var seconds = "0" + date.getSeconds();
        var formattedTime = hours.substr(-2) + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);
        return formattedTime;

    }


    playVideo() {
        this.playerRef.current.play();
        console.log("play")
    }

    pauseVideo() {
        this.playerRef.current.pause();
        console.log("pause")
    }

    toggleControls() {
        this.playerRef.current.controls = !this.playerRef.current.controls;
        console.log("controls")
    }

    render() {

        const {error, isLoaded, chItem} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            $('[data-theme="dark"] body').css("background-color", "transparent");
            $('[data-theme="dark"]').css("background-color", "transparent");

            let playerURL = chItem.streams !== null ? chItem.streams[0].src : ""
            // let programTitle = chItem.currentProgram.title
            // let channelId = chItem.id
            return (
                <div>
                    <MetaTags>
                        <title>{'پخش زنده شبکه ' + chItem.name}</title>
                        <meta id="meta-description" name="description" content={'پخش زنده شبکه ' + chItem.name}/>
                        <meta id="og-title" property="og:title" content={'پخش زنده شبکه ' + chItem.name}/>
                        {/*<meta id="og-image" property="og:image" content="path/to/image.jpg"/>*/}
                    </MetaTags>
                    <img
                        onClick={() => window.open("https://sepehr.irib.ir", "_blank")}
                        src="/LiveLogo.png"
                        height="50"
                        width="50"
                        style={{
                            float: "right",
                            position: "absolute",
                            marginRight: "10px",
                            marginTop: "10px",
                            zIndex: 1,
                            cursor: "pointer"
                        }}
                    />
                    <ReactHlsPlayer
                        playerRef={this.playerRef}
                        src={playerURL}
                        autoPlay={false}
                        controls={true}
                        width="100%"
                        height="100%"
                    />
                </div>
            );
        }
    }
}
