import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";
import parse from "html-react-parser";

export class VNMarquee extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
            catId: this.props.catId,
            pageSize: this.props.pageSize,
        };
        this.catId = this.props.catId;
        this.pageSize = this.props.pageSize;
        this.createStructure();

    }

    createStructure() {

        generateRequest("https://sepehrapi.irib.ir/v3/videonews?category_id=" +
            this.catId + "&page_size=" +
            this.pageSize + "&page=1&include_media_resources=true&include_details=true&order_types=RELEASE_DATE_DESC", 'GET')
            .then(
                (sliderResult) => {
                    this.setState({
                        isLoaded: true,
                        items: sliderResult
                    });
                },
                (error) => {
                    return null;
                }
            )
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.initCr();
    }

    componentDidMount() {
        this.initCr();
    }

    initCr() {
        $('.owl-carousel-one').owlCarousel({
            items: 1,
            stagePadding: 0,
            rtl: true,
            loop: true,
            autoplay: true,
            autoWidth: false,
            dots: true,
            center: true,
            margin: 8,
            // nav: true,
            // navText : ["<i class='fa fa-chevron-left'></i>","<i class='fa fa-chevron-right'></i>"],
            // navText: [('<i class="fa fa-angle-right"></i>'), ('<i class="fa fa-angle-left"></i>')],
            animateIn: 'fadeIn',
            animateOut: 'fadeOut'
        })
    }

    render() {
        const {error, isLoaded, items, catId, catTitle} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            let allText = "<h5> ";
            // let allText = '';
            let mItems = items.list;
            let mItemsLen = items.list.length;
            for (let i = 0; i < mItemsLen; i++) {
                // allText += mItems[i].title + "&emsp | &emsp";
                let newsUrl = "/videonews/play/" + catId + "/" + mItems[i].id;
                // allText += '<NavLink to="'+ "/videonews/play/" + catId + "/" + mItems[i].id + '>';
                // allText += '<div class="mx-5">';
                allText += '<a class="text-light " href="' + newsUrl + '">';
                // allText += mItems[i].title + '.';
                allText += mItems[i].title + '\t';
                allText += '</a>';
                allText += '<img style="margin-right: 100px; margin-left: 20px" height="30" width="30" src="' + mItems[i].provider.logo + '" alt=""/>';
                // allText += '</div>';
                // allText += '</NavLink>';
                console.log(allText)
                // allText += mItems[i].title;
            }
            allText += " </h5>";
            return (
                <div className="pt-3 pb-5">

                    <div className="card single-product-card p-3 ">
                        <div className="position-absolute card single-product-card p-1" style={{width: "100px", height: "40px"}}>
                            <spam className="text-light" >
                                سرخط اخبار:
                            </spam>
                        </div>
                        <marquee width="100%" direction="right" height="auto" scrollamount="10">
                            <div style={{direction: "ltr", textDirection: "right"}}>
                                {parse(allText)}
                                {/*{allText}*/}
                                {/*{{['<p> ', allText, ' </p>']}}*/}
                            </div>
                        </marquee>
                    </div>
                </div>
            );
        }
    }
}
