import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";

export class VideoNewsCarouselItems extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
            catId: this.props.catId,
            catTitle: this.props.catTitle,
            responsive: this.props.responsive
        };
        this.catId = this.props.catId;
        this.catTitle = this.props.catTitle;
        this.responsive = this.props.responsive;
        this.createStructure();
    }

    createStructure() {
        generateRequest("https://sepehrapi.irib.ir/v3/videonews?category_id=" +
            this.catId + "&order_types=RELEASE_DATE_DESC&page_size=12&page=1&include_media_resources=true&include_details=true", 'GET')
            .then(
                (vnResult) => {
                    this.setState({
                        isLoaded: true,
                        items: vnResult
                    });
                },
                (error) => {
                    return null;
                }
            )
    }

    componentDidMount() {
        this.initCr();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.initCr();
    }

    initCr() {
        const ocState1 = {
            responsive: {
                0: {
                    items: 1.5
                },
                320: {
                    items: 1.5
                },
                420: {
                    items: 1.5
                },
                450: {
                    items: 1.5
                },
                550: {
                    items: 1.5
                },
                750: {
                    items: 3.5
                },
                850: {
                    items: 3.5
                },
                992: {
                    items: 3.5
                },
                1200: {
                    items: 3.5
                }
            }
        };

        // console.log("this.responsive")
        // console.log(this.responsive)

        $('.owl-carousel-NS').owlCarousel({
            // responsive: ocState.responsive,
            responsive: this.responsive,
            stagePadding: 0,
            rtl: true,
            loop: true,
            autoplay: false,
            autoWidth: false,
            dots: true,
            center: false,
            margin: 8,
            animateIn: 'fadeIn',
            animateOut: 'fadeOut'
        })

    }

    render() {

        const {error, isLoaded, items, catId, catTitle} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            return (
                <div className="pb-3">
                    <div className="mb-3">
                        <NavLink to={"/videonews/play/" + catId}
                                 className="text-light" style={{float: "left", fontSize: "110%"}}>
                            مشاهده همه »

                        </NavLink>
                        <h6>
                            {catTitle}
                        </h6>
                    </div>
                    <div className='owl-carousel-NS owl-carousel'>
                        {
                            items.list.map((item) => {
                                return (
                                    <div className="item" style={{maxHeight: "280px"}}>
                                        <div className="itemContainer"
                                             style={{
                                                 position: "relative",
                                                 width: "100%",
                                                 paddingTop: "56.25%",
                                                 aspectRatio: "16 / 9",
                                             }}>
                                            <div className="itemContents"
                                                 style={{
                                                     position: "absolute",
                                                     top: "0",
                                                     left: "0",
                                                     bottom: "0",
                                                     right: "0",
                                                 }}
                                            >
                                                <NavLink to={"/videonews/play/" + catId + "/" + item.id}>
                                                    <img style={{
                                                        borderRadius: "5px",
                                                        height: "100%",
                                                        width: "100%",
                                                        objectFit: "fill"
                                                    }}
                                                         src={item.cover}/>
                                                    <div className="single-hero-slide">
                                                        <div style={{
                                                            borderRadius: "5px",
                                                            background: "linear-gradient(180deg, rgba(87,87,87,0.2189250700280112) 0%, rgba(0,0,0,0.6979166666666667) 100%)",
                                                            position: "fixed", bottom: 0,
                                                            width: "100%"
                                                        }}>
                                                            <h6 className="m-2"
                                                                style={{
                                                                    direction: "rtl",
                                                                    textAlign: "center",
                                                                    textOverflow: "ellipsis",
                                                                    whiteSpace: "nowrap",
                                                                    overflow: "hidden",
                                                                }}>
                                                                {item.title}
                                                            </h6>
                                                        </div>
                                                    </div>
                                                </NavLink>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            );
        }
    }
}
