import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";

export class VideoNewsSlideShowItems extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
            catId: this.props.catId,
            catTitle: this.props.catTitle,
            pageSize: this.props.pageSize
        };
        this.catId = this.props.catId;
        this.catTitle = this.props.catTitle;
        this.pageSize = this.props.pageSize;
        this.createStructure();

    }

    createStructure() {

        generateRequest("https://sepehrapi.irib.ir/v3/videonews?category_id=" +
            this.catId + "&page_size=" +
            this.pageSize + "&page=1&include_media_resources=true&include_details=true&order_types=RELEASE_DATE_DESC", 'GET')
            .then(
                (sliderResult) => {
                    this.setState({
                        isLoaded: true,
                        items: sliderResult
                    });
                },
                (error) => {
                    return null;
                }
            )
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.initCr();
    }

    componentDidMount() {
        this.initCr();
    }

    initCr() {
        $('.owl-carousel-one').owlCarousel({
            items: 1,
            stagePadding: 0,
            rtl: true,
            loop: true,
            autoplay: true,
            autoWidth: false,
            dots: true,
            center: true,
            margin: 8,
            // nav: true,
            // navText : ["<i class='fa fa-chevron-left'></i>","<i class='fa fa-chevron-right'></i>"],
            // navText: [('<i class="fa fa-angle-right"></i>'), ('<i class="fa fa-angle-left"></i>')],
            animateIn: 'fadeIn',
            animateOut: 'fadeOut'
        })
    }

    render() {
        const {error, isLoaded, items, catId, catTitle} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            return (
                <div className="pb-2">
                    <h6 className="lead">
                        {catTitle}
                    </h6>
                    <div className='owl-carousel-one owl-carousel'>
                        {
                            items.list.map((item) => {
                                return (
                                    <div className="single-hero-slide item">
                                        <NavLink to={"/videonews/play/" + catId + "/" + item.id}>
                                            <img style={{
                                                height: "100%",
                                                width: "100%",
                                                objectFit: "fill"
                                            }}
                                                 src={item.cover}/>
                                            <div style={{
                                                // background: rgb(87,87,87);
                                                background: "linear-gradient(180deg, rgba(87,87,87,0.2189250700280112) 0%, rgba(0,0,0,0.6979166666666667) 100%)",
                                                position: "absolute", bottom: 0,
                                                width: "100%"
                                            }}>
                                                <h6 className="lead"
                                                    style={{
                                                        direction: "rtl",
                                                        textAlign: "center",
                                                        textOverflow: "ellipsis",
                                                        whiteSpace: "nowrap",
                                                        overflow: "hidden",
                                                    }}>
                                                    {item.title}
                                                </h6>
                                            </div>
                                        </NavLink>
                                    </div>

                                )
                            })
                        }
                    </div>
                </div>
            );
        }
    }
}
