import React from "react";
import {NavLink} from "react-router-dom";
// import ReactHlsPlayer from "react-hls-player";
// import ReactPlayer from 'react-player'
import ReactHlsPlayer from "react-hls-player";
import {generateRequest} from "../Main";
import {LiveCarouselItems} from "../ViewComponents/LiveCarouselItems";
import {LivePlayer} from "../ViewComponents/LivePlayer";
import MetaTags from "react-meta-tags";


export class ContentLivePlay extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            chItem: [],
            epgItems: [],
            programItem: null,
        };
    }

    fetchPageContent() {
        const channelid = this.props.match.params.channelid == null ? 2 : this.props.match.params.channelid;
        const programid = this.props.match.params.programid == null ? 0 : this.props.match.params.programid;
        if (programid == 0) {
            generateRequest("https://sepehrapi.irib.ir/v3/channels/?list=" +
                channelid + "&include_media_resources=true&include_details=true", 'GET')
                .then(
                    (chResult) => {
                        var today = new Date();
                        var date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
                        generateRequest("https://sepehrapi.irib.ir/v3/epg/tvprogram?channel_id=" +
                            chResult.list[0].id + "&date=" + date, 'GET')
                            .then(
                                (epgResult) => {
                                    this.setState({
                                        isLoaded: true,
                                        epgItems: epgResult.list,
                                        chItem: chResult.list[0],
                                        programItem: null
                                    });

                                },
                                (error) => {
                                    this.setState({
                                        isLoaded: true,
                                        error
                                    });
                                }
                            )
                    },
                    (error) => {
                        this.setState({
                            isLoaded: true,
                            error
                        });
                    }
                )
        } else {
            window.scrollTo(0, 0)
            var today = new Date();
            var date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
            generateRequest("https://sepehrapi.irib.ir/v3/epg/tvprogram?channel_id=" + channelid + "&date=" + date, 'GET')
                .then(
                    (epgResult) => {
                        generateRequest("https://sepehrapi.irib.ir/v3/recordings?channel_id=" +
                            channelid + "&program_id=" + programid, 'GET')
                            .then(
                                (programResult) => {
                                    // this.state.isLoaded = true;
                                    // console.log(this.state.isLoaded)
                                    this.setState({
                                        isLoaded: true,
                                        epgItems: epgResult.list,
                                        chItem: [],
                                        programItem: programResult.list[0]
                                    });


                                    // this.self.current.window.scrollTo(0, 0)
                                    // ReactDom.findDOMNode(this).scrollIntoView();
                                },
                                (error) => {
                                    this.setState({
                                        isLoaded: true,
                                        error
                                    });
                                }
                            )
                    },
                    (error) => {
                        this.setState({
                            isLoaded: true,
                            error
                        });
                    }
                )


        }
    }

    componentDidMount() {
        this.state.isLoaded = false;
        $('#preloaderContainer').show();
        this.fetchPageContent();

    }

    componentWillReceiveProps(nextProps) {
        // console.log("componentWillReceiveProps")
        this.props = nextProps;

        this.state = {
            isLoaded: false,
        };
        $('#preloaderContainer').show();
        // console.log("Show Loader")
        // this.state.isLoaded = false;
        // if (!!this.player) {
        //     this.player.dispose();
        // }
        this.fetchPageContent();

    }

    componentWillUnmount() {
        // if (!!this.player) {
        //     this.player.destroy();
        // }
    }


    componentDidUpdate(prevProps) {
        // console.log("componentDidUpdate")
        // this.loadPlayerContent();
        // setTimeout(() => {
        //     this.loadPlayerContent();
        // }, 0)
    }


    formatDate(unix_timestamp) {
        var date = new Date(unix_timestamp);
        var hours = "0" + date.getHours();
        var minutes = "0" + date.getMinutes();
        var seconds = "0" + date.getSeconds();
        var formattedTime = hours.substr(-2) + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);
        return formattedTime;

    }

    loadDetails(curProgramId, channelid) {
        {
            generateRequest("https://sepehrapi.irib.ir/v3/epg/tvprogram?channel_id=" + channelid,
                'GET')
                .then(
                    (nowEpgItem) => {
                        var epgItem = nowEpgItem.list[0];
                        var itemDesc = epgItem.descFull != null ? epgItem.descFull : " بدون توضیحات ";
                        $('#curProgramDesc' + curProgramId).html(itemDesc);
                    },
                    (error) => {
                        this.setState({
                            isLoaded: true,
                            error
                        });
                    }
                )
        }
    }

    render() {
        const {error, isLoaded, chItem, epgItems, programItem} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {
            let playerURL = "";
            let programTitle = "";
            let channelId = "";
            if (programItem == null) {
                playerURL = chItem.streams !== null ? chItem.streams[0].src : ""
                programTitle = chItem.currentProgram.title
                channelId = chItem.id
            } else {
                playerURL = programItem.mediaResources !== null ? programItem.mediaResources[0].src : ""
                programTitle = programItem.title
                channelId = parseInt(programItem.idChannel)
            }
            // console.log(playerURL)
            const epgItemCount = epgItems.length;
            const epgItemsHTML = [];
            let nowEpgItem = null;
            let selectedEpgItem = null;
            if (epgItemCount > 0) {
                for (let i = 0; i < epgItemCount; i++) {
                    let epgItem = epgItems[i];
                    let detailBtn = ""
                    let detailClass = ""
                    if (epgItem.state == "past") {
                        detailBtn =
                            <NavLink to={"/live/play/" + channelId + "/" + epgItem.id}>
                                <button
                                    className="btn btn-circle btn-primary m-auto d-flex align-items-center justify-content-center"
                                    type="button" data-bs-toggle="modal"
                                    data-bs-target="#bootstrapBasicModal">
                                    <svg width="18" height="18" viewBox="0 0 16 16"
                                         className="bi bi-easel" fill="currentColor"
                                         xmlns="http://www.w3.org/2000/svg">
                                        <path
                                            d="M8.473.337a.5.5 0 0 0-.946 0L6.954 2h2.092L8.473.337zM12.15 11h-1.058l1.435 4.163a.5.5 0 0 0 .946-.326L12.15 11zM8.5 11h-1v2.5a.5.5 0 0 0 1 0V11zm-3.592 0H3.85l-1.323 3.837a.5.5 0 1 0 .946.326L4.908 11z"/>
                                        <path fillRule="evenodd"
                                              d="M14 3H2v7h12V3zM2 2a1 1 0 0 0-1 1v7a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V3a1 1 0 0 0-1-1H2z"/>
                                    </svg>
                                </button>
                            </NavLink>;
                        detailClass = "row card-body p-1 m-0"
                    }
                    else if (epgItem.state == "now") {
                        detailBtn = ""
                        detailClass = "row card-body p-1 m-0 bg-dark"
                        nowEpgItem = epgItem;
                    } else if (epgItem.state == "future") {
                        detailBtn = ""
                        detailClass = "row card-body p-1 m-0"
                    }
                    epgItemsHTML.push(
                        <div className="card product-details-card">
                            <div className="card-body p-0">
                                <div className="page-content-wrapper m-0">
                                    <div className="card m-1 p-0 alert" role="alert">
                                        <div className={detailClass}>
                                            <div className="col-9" style={{float: "right"}}>
                                                <p className="text-truncate p-0 m-0">
                                                    {epgItem.title}
                                                </p>
                                                <p className="p-0 m-0">
                                                    {this.formatDate(epgItem.start)}
                                                </p>
                                            </div>
                                            <div className="col-3" style={{float: "right", margin: "auto"}}>
                                                {detailBtn}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                }
            } else {
                epgItemsHTML.push(
                    <div className="alert unread custom-alert-3 alert-primary">
                        <div className="alert-text w-75">
                            <h6 className="text-truncate">
                                بدون برنامه
                            </h6>
                            <h7>
                            </h7>
                            <span className="text-truncate"></span>
                        </div>
                    </div>
                )
            }

            $('#preloaderContainer').hide();

            if (window.innerWidth > 992) {
                // if (programItem == null) {
                // }
                // else {
                //     playerURL = chItem.streams !== null ? chItem.streams[0].src : ""
                //     programTitle = chItem.currentProgram.title
                //     channelId = chItem.id
                // }
                if (nowEpgItem != null) {
                    if (!nowEpgItem.descSummary.toString()
                        || nowEpgItem.descSummary.toString().length === 0)
                        nowEpgItem.descSummary = "بدون توضیحات"
                } else {
                    nowEpgItem = {}
                    nowEpgItem.title = "بدون توضیحات"
                    nowEpgItem.descSummary = "بدون توضیحات"
                    nowEpgItem.start = 0
                }

                return (
                    <div className="container-lg">
                        <MetaTags>
                            <title>{'پخش زنده شبکه ' + chItem.name}</title>
                            <meta id="meta-description" name="description" content={'پخش زنده شبکه ' + chItem.name}/>
                            <meta id="og-title" property="og:title" content={'پخش زنده شبکه ' + chItem.name}/>
                            {/*<meta id="og-image" property="og:image" content="path/to/image.jpg"/>*/}
                        </MetaTags>
                        <div className="page-content-wrapper py-3">
                            <div className="row">
                                <div className="col-4">
                                    <div className="card product-details-card ">
                                        <div className="card-body p-0" id="playerBody">
                                            <div className="page-content-wrapper m-0">
                                                <div className="notification-area">
                                                    <nav className="scrollspy-indicatiors p-0 m-0"
                                                         id="scrollspyWrapper">
                                                        <ul className="d-flex p-1 ">
                                                            <li>
                                                                <p className="nav-link m-0 " style={{width: "115px"}} href="#sec1">برنامه های
                                                                    امروز</p>
                                                            </li>
                                                        </ul>
                                                    </nav>
                                                    <div className="data-scrollspy m-0 p-0" data-bs-spy="scroll"
                                                         data-bs-target="#scrollspyWrapper" data-bs-offset="0"
                                                         style={{height: "560px"}}>
                                                        {/*<h5 id="sec1">برنامه های امروز</h5>*/}
                                                        {epgItemsHTML}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-8">
                                    <LivePlayer
                                        programTitle={programTitle}
                                        mediaUrl={playerURL}
                                        nowEpgItem={nowEpgItem}
                                        chItem={chItem}
                                    />
                                </div>
                            </div>
                            <div className="row">
                                <LiveCarouselItems
                                    catId={2}
                                    withTitle={false}
                                    withCats={true}
                                    pageSize={100}/>
                            </div>
                        </div>
                    </div>
                );
            } else {
                return (
                    <div className="page-content-wrapper py-3">
                        <div className="card product-details-card mb-1">
                            <div className="card-body p-0">
                                <img className="p-1"
                                     style={{
                                         height: "50px",
                                         // width: "100px",
                                         objectFit: "fill",
                                         float: "right",
                                         marginRight: "12px"
                                     }}
                                     src={chItem.poster}/>
                                <h5 id="curProgramTitle"
                                    style={{
                                        // fontSize: "90%",
                                        // maxWidth: "300px",
                                        textOverflow: "ellipsis",
                                        whiteSpace: "nowrap",
                                        overflow: "hidden",
                                        padding: "10px"
                                    }}
                                    className="">
                                    { chItem.name !== undefined ? "پخش زنده شبکه " + chItem.name + " - " + programTitle: "بازپخش"}
                                </h5>
                                <div className="card-body p-0" id="playerBody">
                                    {/*<ReactPlayer url={playerURL} />*/}
                                    {/*<ReactPlayer url="https://s-cloud.irib.ir/img/VAR.mp4" />*/}

                                    <ReactHlsPlayer
                                        src={playerURL}
                                        autoPlay={false}
                                        controls={true}
                                        width="100%"
                                        height="auto"
                                    />
                                    {/*<video id="myPlayer" ref={this.self}>*/}
                                    {/*    /!*<source src={vnItem.media_resources.mediaResources[0].src} type='video/mp4'/>*!/*/}
                                    {/*    <source src={playerURL}*/}
                                    {/*            type='application/x-mpegURL'/>*/}
                                    {/*</video>*/}
                                </div>
                            </div>
                        </div>

                        <div className="card product-details-card ">
                            <div className="card-body p-0" id="playerBody">
                                <div className="page-content-wrapper m-0">
                                    <div className="notification-area">
                                        <nav className="scrollspy-indicatiors p-0 m-0" id="scrollspyWrapper">
                                            <ul className="d-flex p-1">
                                                <li>
                                                    <p className="nav-link m-0" href="#sec1">برنامه های امروز</p>
                                                </li>
                                            </ul>
                                        </nav>
                                        <div className="data-scrollspy m-0 p-0" data-bs-spy="scroll"
                                             data-bs-target="#scrollspyWrapper" data-bs-offset="0"
                                             style={{height: "100%"}}>
                                            {/*<h5 id="sec1">برنامه های امروز</h5>*/}
                                            {epgItemsHTML}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            }
            // console.log("Hide Loader")

        }

    }
}
