import React from "react";
import {NavLink} from "react-router-dom";
import oauthSignature from "oauth-signature"
import {generateRequest} from "../Main";

export class ContentLive extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            chItems: [],
            chCatItems: [],
            chItemsPagination: null
        };
    }

    getContentLiveTotalItems() {
        generateRequest("https://sepehrapi.irib.ir/v3/channels/categories", 'GET')
            .then(
                (chCatResult) => {
                    this.state.chCatItems = chCatResult;
                    this.getContentLiveItems();
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            )
    }

    getContentLiveItems() {
        const queryParams = new URLSearchParams(window.location.search);
        const pageNumber = queryParams.get('page') == null ? 1 : queryParams.get('page');
        const catId = this.props.match.params.catId;
        const chCatResult = this.state.chCatItems;
        const pageSize = 100;
        let targetCatId = catId == null ? chCatResult.list[0].id : catId;

        generateRequest("https://sepehrapi.irib.ir/v3/channels/" + targetCatId
            + "?page_size=" + pageSize + "&page=" + pageNumber + "&include_media_resources=true&include_details=true",
            'GET')
            .then(
                (chResult) => {
                    this.setState({
                        isLoaded: true,
                        chItems: chResult.list,
                        chItemsPagination: {
                            'rowCount': chResult.rowCount,
                            'pageNumber': chResult.pageNumber,
                            'pageSize': chResult.pageSize,
                            'nextPageLink': chResult.nextPageLink,
                        },
                        chCatItems: chCatResult.list
                    });
                }
            )
            .catch((error) => {
                console.log(error)
            });
        // .then(res => res.json())

        //     (error) => {
        //         console.log(error)
        //         this.setState({
        //             isLoaded: true,
        //             error
        //         });
        //     }
        // )
    }

    componentWillReceiveProps(nextProps) {
        this.props = nextProps;
        // console.log("componentWillReceiveProps");
        // document.getElementById('preloader').appendChild
        // $('#preloader').fadeOut('1000', function () {
        //     $(this).remove();
        // });
        // $('#preloaderContainer').append(preLoaderHTML());
        $('#preloaderContainer').show();
        this.state.isLoaded = false;
        this.getContentLiveTotalItems();

    }

    componentDidUpdate(prevProps) {

        // console.log("componentDidUpdate");
        //
        // console.log("this.props START");
        // console.log(this.props);
        // console.log(prevProps);
        // console.log("this.props END");
        // if (prevProps.location.key !== this.props.location.key) {
        //     //     const { post } = this.props;
        //     //     this.setState({
        //     //         title: post.title,
        //     //         content: post.content
        //     //     });
        //     // this.componentDidMount();
        //     this.getContentLiveTotalItems();
        // }
    }

    componentDidMount() {
        $('#preloaderContainer').show();
        this.getContentLiveTotalItems();

    }


    render() {

        const {error, isLoaded, chItems, chItemsPagination, chCatItems} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            const paginationItems = [];
            const allPageCount = Math.ceil(chItemsPagination.rowCount / chItemsPagination.pageSize);

            for (let i = 0; i < allPageCount; i++) {
                // paginationItems.push(<li className="page-item"><a className="page-link" href={"?page=" + (i+1)}>{i+1}</a></li>)
                paginationItems.push(<li className="page-item">
                    <NavLink to={"?page=" + (i + 1)} className="page-link" style={{fontSize: "18px"}}>
                        {i + 1}
                    </NavLink>
                </li>)
            }
            $('#preloaderContainer').hide();
            return (
                <div className="container-lg">
                    <div className="page-content-wrapper py-3">
                        <div className="pb-3">
                            <h3>دسته بندی های پخش زنده</h3>
                            {chCatItems.map(chCatItem => (
                                // <a
                                //     href={"/live/" + chCatItem.id}
                                //     className="badge bg-light text-dark"
                                //     style={{margin:"2px", fontSize: "16px"}}>
                                //     {chCatItem.name}
                                // </a>
                                <NavLink
                                    to={"/live/" + chCatItem.id}
                                    className="badge bg-light text-dark"
                                    style={{margin: "2px", fontSize: "16px"}}>
                                    {chCatItem.name}
                                </NavLink>
                            ))}
                        </div>
                        <div className="shop-pagination pb-3">
                            <div className="d-flex align-items-center justify-content-between">
                                <small className="ps-1">
                                    نمایش {chItems.length} از {chItemsPagination.rowCount}
                                </small>
                            </div>
                        </div>

                        <div className="top-products-area">
                            <div className="row g-3">
                                {chItems.map(item => (
                                    <div className="col-6 col-sm-4 col-lg-3">
                                        <div className="card single-product-card">
                                            <NavLink to={"/live/play/" + item.id}>
                                                <div className="card-body card-content-live p-3 liveOverlayContainer"
                                                     style={{height: '200px'}}>
                                                    <div className="liveOverlay">
                                                        <img srcSet={item.preview}
                                                             alt={item.title}
                                                             style={{
                                                                 width: '100%',
                                                                 height: '100%',
                                                                 borderRadius: "5px",
                                                             }}/>
                                                    </div>
                                                    <div className="product-thumbnail d-block">
                                                        <img srcSet={item.icon} alt={item.title}
                                                             style={{height: '100px'}}/>
                                                    </div>
                                                    <div className="product-title d-block text-truncate">
                                                        {item.name}
                                                    </div>
                                                    <p style={{
                                                        textOverflow: "ellipsis",
                                                        whiteSpace: "nowrap",
                                                        overflow: "hidden"
                                                    }}>
                                                        {item.currentProgram.title}
                                                    </p>

                                                </div>
                                            </NavLink>

                                        </div>
                                    </div>

                                ))}

                            </div>
                        </div>

                        <div className="shop-pagination pt-3">
                            <div className="card">
                                <div className="card-body py-3">
                                    <nav aria-label="Page navigation example">
                                        <ul className="pagination pagination-two justify-content-center">
                                            <li className="page-item">
                                                {/*<NavLink className="page-link" to={""} aria-label="Previous">*/}

                                                {/*    <svg width="16" height="16" viewBox="0 0 16 16"*/}
                                                {/*         className="bi bi-chevron-right" fill="currentColor"*/}
                                                {/*         xmlns="http://www.w3.org/2000/svg">*/}
                                                {/*        <path fillRule="evenodd"*/}
                                                {/*              d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z"/>*/}
                                                {/*    </svg>*/}
                                                {/*</NavLink>*/}
                                            </li>
                                            {
                                                paginationItems
                                            }

                                            <li className="page-item">
                                                {/*<NavLink className="page-link" to={""} aria-label="Next">*/}
                                                {/*    <svg width="16" height="16" viewBox="0 0 16 16"*/}
                                                {/*         className="bi bi-chevron-left" fill="currentColor"*/}
                                                {/*         xmlns="http://www.w3.org/2000/svg">*/}
                                                {/*        <path fillRule="evenodd"*/}
                                                {/*              d="M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z"/>*/}
                                                {/*    </svg>*/}
                                                {/*</NavLink>*/}
                                            </li>
                                        </ul>
                                    </nav>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
                ;
        }
    }

}
