import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";

export class SimpleBannerItems extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            item: null
        };
        this.createStructure();
    }

    createStructure() {
        this.state.item = this.props.item;
        this.state.isLoaded = true;
        // this.setState({
        //     isLoaded: true,
        //     items: tempItems,
        // });
    }

    render() {
        const {error, isLoaded, item} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            // let navLink = "";
            // if (item.onClick.field == 1)
            //     navLink = "";
            // else if (item.onClick.field == 2)
            //     navLink = ""
            return (
                <div className="pb-3 p-0 m-0">
                    <div className="p-1">
                        <NavLink to={item.onClick.field}>
                            <img className="card-img-top" srcSet={item.src}
                                 alt={item.title}
                                 style={{
                                     borderRadius: "5px",
                                 }}
                            />

                        </NavLink>
                    </div>
                </div>
            );
        }
    }
}
