import React from 'react';
import {NavLink} from "react-router-dom";
import {isLogin} from "../Main";

const SideNavWrapper = () => {

    function _handleItemPress() {
        $("#sidenavWrapper").removeClass("nav-active")
        $(".sidenav-black-overlay").removeClass("active")
    }

    let profileHtml = '';
    let loginHtml = '';
    let logoutHtml = '';

    if (isLogin()) {
        let phoneNumber = sessionStorage.getItem('phoneNumber');
        profileHtml =
            <div className="sidenav-profile">
                <div className="sidenav-style1"></div>
                <div className="user-profile"><img src="/img/bg-img/user1.png" alt=""/></div>
                <div className="user-info">
                    <h6 className="user-name mb-0">کاربر
                        <strong className="ms-1" style={{textAlign: "left"}}> {phoneNumber} </strong>
                    </h6>
                    <span>خووش آمدید</span>
                </div>
            </div>
        loginHtml = ''
        logoutHtml =
            <NavLink to={'/logout'}
                     onClick={_handleItemPress}>
                <svg width="18" height="18" viewBox="0 0 16 16" className="bi bi-box-arrow-right"
                     fill="currentColor"
                     xmlns="http://www.w3.org/2000/svg">
                    <path fillRule="evenodd"
                          d="M10 12.5a.5.5 0 0 1-.5.5h-8a.5.5 0 0 1-.5-.5v-9a.5.5 0 0 1 .5-.5h8a.5.5 0 0 1 .5.5v2a.5.5 0 0 0 1 0v-2A1.5 1.5 0 0 0 9.5 2h-8A1.5 1.5 0 0 0 0 3.5v9A1.5 1.5 0 0 0 1.5 14h8a1.5 1.5 0 0 0 1.5-1.5v-2a.5.5 0 0 0-1 0v2z"/>
                    <path fillRule="evenodd"
                          d="M15.854 8.354a.5.5 0 0 0 0-.708l-3-3a.5.5 0 0 0-.708.708L14.293 7.5H5.5a.5.5 0 0 0 0 1h8.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3z"/>
                </svg>
                خروج
            </NavLink>
    } else {
        profileHtml =
            <div className="sidenav-profile">
                <div className="sidenav-style1"></div>
                <div className="user-profile"><img src="/img/bg-img/user1.png" alt=""/></div>
                <div className="user-info">
                    <h6 className="user-name mb-0">کاربر مهمان</h6><span>خووش آمدید</span>
                </div>
            </div>
        loginHtml =
            <NavLink to={"/login"}
                     onClick={_handleItemPress}>
                <svg width="18" height="18" viewBox="0 0 16 16" className="bi bi-house-door" fill="currentColor"
                     xmlns="http://www.w3.org/2000/svg">
                    <path fillRule="evenodd"
                          d="M7.646 1.146a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 .146.354v7a.5.5 0 0 1-.5.5H9.5a.5.5 0 0 1-.5-.5v-4H7v4a.5.5 0 0 1-.5.5H2a.5.5 0 0 1-.5-.5v-7a.5.5 0 0 1 .146-.354l6-6zM2.5 7.707V14H6v-4a.5.5 0 0 1 .5-.5h3a.5.5 0 0 1 .5.5v4h3.5V7.707L8 2.207l-5.5 5.5z"/>
                    <path fillRule="evenodd" d="M13 2.5V6l-2-2V2.5a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5z"/>
                </svg>
                ورود
            </NavLink>
        logoutHtml = ''
    }
    return (
        <div className="sidenav-wrapper" id="sidenavWrapper">
            <div className="go-back-btn" id="goBack">
                <svg className="bi bi-x" width="24" height="24" viewBox="0 0 16 16" fill="currentColor"
                     xmlns="http://www.w3.org/2000/svg">
                    <path fillRule="evenodd"
                          d="M11.854 4.146a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708-.708l7-7a.5.5 0 0 1 .708 0z"></path>
                    <path fillRule="evenodd"
                          d="M4.146 4.146a.5.5 0 0 0 0 .708l7 7a.5.5 0 0 0 .708-.708l-7-7a.5.5 0 0 0-.708 0z"></path>
                </svg>
            </div>
            {profileHtml}
            <ul className="sidenav-nav ps-0">
                <li>
                    {loginHtml}
                </li>

                <li>
                    <div className="night-mode-nav">
                        <svg width="18" height="18" viewBox="0 0 16 16" className="bi bi-moon" fill="currentColor"
                             xmlns="http://www.w3.org/2000/svg">
                            <path fillRule="evenodd"
                                  d="M14.53 10.53a7 7 0 0 1-9.058-9.058A7.003 7.003 0 0 0 8 15a7.002 7.002 0 0 0 6.53-4.47z"/>
                        </svg>
                        <span style={{margin: '2px'}}>حالت شب</span>
                        <div className="form-check form-switch">
                            <input className="form-check-input form-check-success" type="checkbox" id="darkSwitch" onClick={_handleItemPress}/>
                        </div>
                    </div>
                </li>
                <li>
                    {logoutHtml}
                </li>
            </ul>

            <div className="copyright-info">
                <p>&copy; 2021 <a href="#">IRIB</a></p>
            </div>
        </div>
    );
}

export
{
    SideNavWrapper
}
    ;
