import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";

export class BFullscreenSlideShow extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
            catId: this.props.catId,
            catTitle: this.props.catTitle
        };
        this.createStructure();
    }

    createStructure() {
        this.state.items = this.props.items;
        this.state.isLoaded = true;
    }

    componentDidMount() {
        this.initCr();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.initCr();
    }

    initCr() {
        $('.carousel').carousel({
            interval: 5000
        })

    }

    render() {
        const {error, isLoaded, items, catId, catTitle} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            let Indicators = [];
            let CR = [];
            Indicators.push(<li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>);
            CR.push(
                <NavLink to={items.data[0].onClick.field}>
                    <div className="carousel-item active">
                        <img className="d-block w-100" src={items.data[0].src} alt=""/>
                    </div>
                </NavLink>);
            for (let i = 1; i < items.data.length; i++) {
                Indicators.push(
                    <li data-target="#carouselExampleIndicators" data-slide-to={i}></li>
                )
                CR.push(
                    <NavLink to={items.data[i].onClick.field}>
                        <div className="carousel-item">
                            <img className="d-block w-100" src={items.data[i].src} alt=""/>
                        </div>
                    </NavLink>
                )
            }
            return (
                <div className="">
                    <div id="carouselExampleIndicators" className="carousel slide BCarousel"
                         data-ride="carousel">
                        <ol className="carousel-indicators">
                            {Indicators}
                        </ol>
                        <div className="carousel-inner">
                            {CR}
                        </div>
                        <a className="carousel-control-prev" href="#carouselExampleIndicators"
                           role="button" data-slide="prev">
                            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span className="sr-only">Previous</span>
                        </a>
                        <a className="carousel-control-next" href="#carouselExampleIndicators"
                           role="button" data-slide="next">
                            <span className="carousel-control-next-icon" aria-hidden="true"></span>
                            <span className="sr-only">Next</span>
                        </a>
                    </div>

                </div>
            );
        }
    }
}
