import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";

export class WebsiteFooter extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
        };
        this.createStructure();
    }

    createStructure() {
        this.state.items = this.props.items;
        this.state.isLoaded = true;
    }

    componentDidMount() {
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
    }

    render() {
        const {error, isLoaded} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {
            return (
                <footer className="footer" style={{
                    position: "absolute",
                    bottom: 0,
                    width: "100%",
                    minHeight: "100px",
                    backgroundColor: "red",
                    color: "white",
                    // background: "linear-gradient(0deg, rgba(1,5,18,1) 0%, rgba(1,5,18,0.69) 65%, rgba(1,5,18,0) 100%)"
                    background: "rgb(1,5,18)"

                }}>
                    <div className="row">
                        <div className="col-4">
                            <div className="row mx-3 pt-1 hotNewsItems">
                                <ul>
                                    <li>
                                        <NavLink to={"/rights/"}>
                                            <span className="text-light text-truncate">قوانین و مقررات</span>
                                        </NavLink>
                                    </li>
                                </ul>
                            </div>
                            <div className="row mx-3 pt-1 hotNewsItems">
                                <ul>
                                    <li>
                                        <NavLink to={"/about-us/"}>
                                            <span className="text-light text-truncate">درباره ما</span>
                                        </NavLink>
                                    </li>
                                </ul>
                            </div>
                            <div className="row mx-3 pt-1 hotNewsItems">

                                <ul>
                                    <li>
                                        <NavLink to={"/contact-us/"}>
                                            <span className="text-light text-truncate">تماس با ما</span>
                                        </NavLink>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div className="col-4">
                            <div className="row mx-3 pt-1 hotNewsItems">
                                <ul>
                                    <li>
                                        <NavLink to={"/app/"}>
                                            <span className="text-light text-truncate">نصب اپلیکیشن</span>
                                        </NavLink>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div className=" col-4">
                            <img className="m-4 p-0" src="/img/sepehr_logo_persian1.svg" alt=""
                                 style={{
                                     margin: "auto",
                                     float: "left",
                                     width: "auto",
                                     height: "50px",
                                     maxWidth: "100%",
                                     maxHeight: "50px",
                                     marginLeft: "8px",
                                     cursor: "pointer"
                                 }}/>
                        </div>
                    </div>
                </footer>
            )
                ;
        }
    }
}
