import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";
import './VNMedHotNewsItems.css'
export class VNMedHotNewsItems extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
            HotNewsCat: this.props.HotNewsCat,
            HotNewsCount: this.props.HotNewsCount,
            HotNewsTitle: this.props.HotNewsTitle,
        };
        this.HotNewsCat = this.props.HotNewsCat;
        this.HotNewsCount = this.props.HotNewsCount;
        this.HotNewsTitle = this.props.HotNewsTitle;
        this.createStructure();

    }

    createStructure() {

        generateRequest("https://sepehrapi.irib.ir/v3/videonews?category_id=" +
            this.HotNewsCat + "&page_size=" + this.HotNewsCount + "&page=1&include_media_resources=true&include_details=true&order_types=RELEASE_DATE_DESC", 'GET')
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        items: result
                    });
                },
                (error) => {
                    return null;
                }
            )
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.initCr();
    }

    componentDidMount() {
        this.initCr();
    }

    initCr() {
        $('.owl-carousel-one').owlCarousel({
            items: 1,
            stagePadding: 0,
            rtl: true,
            loop: true,
            autoplay: true,
            autoWidth: false,
            dots: true,
            center: true,
            margin: 8,
            // nav: true,
            // navText : ["<i class='fa fa-chevron-left'></i>","<i class='fa fa-chevron-right'></i>"],
            // navText: [('<i class="fa fa-angle-right"></i>'), ('<i class="fa fa-angle-left"></i>')],
            animateIn: 'fadeIn',
            animateOut: 'fadeOut'
        })
    }

    render() {
        const {error, isLoaded, items, HotNewsCat, HotNewsTitle} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {
            // let HotNewsImg = <a className="product-thumbnail d-block">
            //     <img src={items.list[0].poster} alt="" style={{height: "300px", width: "100%"}}/>
            //     {/*<span className="badge bg-primary">Hot</span>*/}
            // </a>

            let HotNewsImg = <div className="single-hero-slide item" style={{height: "auto", position: "relative"}}>
                <NavLink to={"/videonews/play/" + HotNewsCat + "/" + items.list[0].id}>
                    <img style={{
                        height: "330px",
                        width: "100%",
                        objectFit: "fill"
                    }}
                         src={items.list[0].poster}/>
                    <div style={{
                        // background: rgb(87,87,87);
                        background: "linear-gradient(180deg, rgba(87,87,87,0.2189250700280112) 0%, rgba(0,0,0,0.6979166666666667) 100%)",
                        position: "absolute", bottom: 0,
                        width: "100%"
                    }}>
                        <h6 className="lead px-2"
                            style={{
                                direction: "rtl",
                                textAlign: "center",
                                textOverflow: "ellipsis",
                                whiteSpace: "nowrap",
                                overflow: "hidden",
                            }}>
                            {items.list[0].title}
                        </h6>
                    </div>
                </NavLink>
            </div>

            items.list.splice(0, 1);
            return (
                <div className="pb-2" style={{height: "650px"}}>
                    <div className="card single-product-card">
                        <h6 className="lead pt-1 m-auto">
                            {HotNewsTitle}
                        </h6>
                        <div className="card-body p-3 hotNewsItems" style={{height: "620px"}}>
                            {HotNewsImg}
                            {
                                items.list.map((item) => {
                                    return (
                                        <ul>
                                            <li>
                                                <NavLink to={"/videonews/play/" + HotNewsCat + "/" + item.id}>
                                                    <h4 className="product-title d-block text-truncate">{item.title}</h4>
                                                </NavLink>
                                            </li>

                                        </ul>
                                    )
                                })
                            }

                            {/*<p className="sale-price">$110<span>$128</span></p>*/}
                            {/*<a className="btn btn-danger btn-sm disabled" href="#">Out of Stock</a>*/}
                        </div>
                    </div>
                </div>
            );
        }
    }
}
