import React from "react";
import {NavLink} from "react-router-dom";
import {generateRequest} from "../Main";

export class LiveCarouselItems extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
            catId: this.props.catId,
            chCatItems: [],
            catTitle: this.props.catTitle,
            withTitle: true,
        };
        this.catId = this.props.catId;
        this.catTitle = this.props.catTitle;
        this.withTitle = this.props.withTitle;
        this.withCats = this.props.withCats !== undefined ? this.props.withCats : false;
        this.pageSize = this.props.pageSize !== undefined ? this.props.pageSize : 12;
        this.createStructure();
    }

    createStructure() {
        generateRequest("https://sepehrapi.irib.ir/v3/channels/" +
            this.catId
            + "?page_size=" + this.pageSize
            + "&page=1&include_media_resources=true&include_details=true", 'GET')
            .then(
                (liveResult) => {
                    if (this.withCats) {
                        generateRequest("https://sepehrapi.irib.ir/v3/channels/categories", 'GET')
                            .then(
                                (chCatResult) => {
                                    this.setState({
                                        isLoaded: true,
                                        items: liveResult.list,
                                        chCatItems: chCatResult.list,
                                    });
                                },
                                (error) => {
                                    this.setState({
                                        isLoaded: true,
                                        error
                                    });
                                }
                            )
                    } else {
                        this.setState({
                            isLoaded: true,
                            items: liveResult.list
                        });
                    }

                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                    return null;
                }
            )
    }


    componentDidMount() {
        this.initCr();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.initCr();
    }

    initCr() {
        const ocState = {
            responsive: {
                0: {
                    items: 2.5
                },
                320: {
                    items: 2.5
                },
                420: {
                    items: 2.5
                },
                450: {
                    items: 3.5
                },
                550: {
                    items: 3.5
                },
                750: {
                    items: 4.5
                },
                850: {
                    items: 4.5
                },
                992: {
                    items: 5.5
                },
                1200: {
                    items: 5.5
                }
            }
        };
        $('.owl-carousel-two').owlCarousel({
            responsive: ocState.responsive,
            stagePadding: 0,
            rtl: true,
            loop: true,
            autoplay: false,
            dots: true,
            center: false,
            autoWidth: false,
            margin: 8,
            // nav: true,
            // navText: ['<i class="fa fa-angle-left" aria-hidden="true"></i>', '<i class="fa fa-angle-right" aria-hidden="true"></i>'],
            animateIn: 'fadeIn',
            animateOut: 'fadeOut'

        })
    }

    getChItems(catId) {
        this.setState({
            isLoaded: false,
        });
        generateRequest("https://sepehrapi.irib.ir/v3/channels/" +
            catId
            + "?page_size=" + this.pageSize
            + "&page=1&include_media_resources=true&include_details=true", 'GET')
            .then(
                (liveResult) => {
                    this.setState({
                        isLoaded: true,
                        items: liveResult.list
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                    return null;
                }
            )
        return false;
        // return function (p1: React.MouseEvent<HTMLButtonElement>) {
        // };
    }

    render() {

        const {error, isLoaded, items, chCatItems, catId, catTitle} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {

            let tTitle = null
            let tCats = null
            if (this.withTitle)
                tTitle =
                    <div className="mb-3">
                        <NavLink to={"/live/" + catId} className="text-light"
                                 style={{float: "left", fontSize: "110%"}}>
                            مشاهده همه »
                        </NavLink>
                        <h6>
                            {catTitle}
                        </h6>
                    </div>

            if (this.withCats)
                tCats =
                    <div className="pb-3">
                        <h3>دسته بندی های پخش زنده</h3>
                        {chCatItems.map(chCatItem => (
                            // <a
                            //     href={"/live/" + chCatItem.id}
                            //     className="badge bg-light text-dark"
                            //     style={{margin:"2px", fontSize: "16px"}}>
                            //     {chCatItem.name}
                            // </a>
                            <span
                                onClick={() => this.getChItems(chCatItem.id)}
                                className="badge bg-light text-dark"
                                style={{margin: "2px", fontSize: "16px", cursor: "pointer"}}>
                                {chCatItem.name}
                            </span>
                        ))}
                    </div>
            return (
                <div className="pt-3 pb-3">
                    {tTitle}
                    {tCats}
                    <div className='owl-carousel-two owl-carousel'>
                        {
                            items.map((item) => {
                                return (
                                    <div className="item">
                                        <div className="itemContainer"
                                             style={{
                                                 position: "relative",
                                                 width: "100%",
                                                 paddingTop: "56.25%",
                                                 aspectRatio: "16 / 9",
                                             }}>
                                            <div className="itemContents"
                                                 style={{
                                                     position: "absolute",
                                                     top: "0",
                                                     left: "0",
                                                     bottom: "0",
                                                     right: "0",
                                                 }}
                                            >
                                                <NavLink to={'/live/play/' + item.id}>
                                                    <img srcSet={item.preview}
                                                         alt={item.title}
                                                         style={{
                                                             borderRadius: "10px",
                                                             // maxHeight: '150px',
                                                             width: '100%',
                                                             height: '100%',
                                                             display: "block",
                                                             margin: "auto"
                                                         }}/>
                                                    <h6 className="card-title m-1"
                                                        style={{
                                                            position: "fixed",
                                                            top: 0,
                                                            direction: "rtl",
                                                            textAlign: "center",
                                                            textOverflow: "ellipsis",
                                                            whiteSpace: "nowrap",
                                                            overflow: "hidden"
                                                        }}
                                                    >
                                                        {item.name}
                                                    </h6>
                                                    <div style={{
                                                        borderRadius: "10px",
                                                        // background: rgb(87,87,87);
                                                        background: "linear-gradient(180deg, rgba(87,87,87,0.2189250700280112) 0%, rgba(0,0,0,0.6979166666666667) 100%)",
                                                        position: "fixed",
                                                        bottom: 0,
                                                        width: "100%"
                                                    }}>

                                                        <img className="card-img-top p-2"
                                                             srcSet={item.icon}
                                                             alt={item.title}
                                                             style={{

                                                                 width: '50px',
                                                                 float: "right"
                                                             }}/>

                                                        <h6 className="card-text mt-3"
                                                            style={{
                                                                fontSize: "small",
                                                                margin: "auto",
                                                                direction: "rtl",
                                                                textAlign: "center",
                                                                textOverflow: "ellipsis",
                                                                whiteSpace: "nowrap",
                                                                overflow: "hidden"
                                                            }}
                                                        >{item.currentProgram.title}
                                                        </h6>
                                                    </div>
                                                </NavLink>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>

            );
        }
    }
}
