import React, {Fragment} from "react";
import {NavLink} from "react-router-dom";
import parse, {htmlToDOM} from 'html-react-parser'
import fluidPlayer from 'fluid-player';
import {generateRequest} from "../Main";
import ReactHlsPlayer from 'react-hls-player';
import ReactDOM from "react-dom";
import {VideoNewsPlayItem} from "../ViewComponents/VideoNewsPlayItem";
import {VideoNewsPlayItem2} from "../ViewComponents/VideoNewsPlayItem2";


export class ContentVideoNewsPlay2 extends React.Component {

    constructor(props) {

        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            vnItem: [],
            vnItems: [],
            catId: null,
        };
        // this.handleLoad = this.handleLoad.bind(this);
        this.self = React.createRef();
        this.player = null;
    }

    fetchPageContent() {
        const pageSize = 25;
        const vnid = this.props.match.params.vnid == null ? 0 : this.props.match.params.vnid;
        const vncid = this.props.match.params.vncid == null ? 0 : this.props.match.params.vncid;
        if (vnid == 0) {
            generateRequest("https://sepehrapi.irib.ir/v3/videonews?category_id=" +
                vncid + "&page=1&page_size=" +
                pageSize + "&include_details=true&include_media_resources=true&order_types=RELEASE_DATE_DESC", 'GET')
                .then(
                    (vnsResult) => {
                        this.setState({
                            isLoaded: true,
                            vnItem: null,
                            vnItems: vnsResult.list,
                            catId: vncid
                        });
                    },
                    (error) => {
                        this.setState({
                            isLoaded: true,
                            error
                        });
                    }
                )
        } else {
            window.scrollTo(0, 0)
            generateRequest("https://sepehrapi.irib.ir/v3/videonews/?item_id_list=" +
                vnid + "&include_media_resources=true&include_details=true", 'GET')
                .then(
                    (vnResult) => {
                        // console.log(vnid)
                        // console.log(vnResult.list[0].title)
                        // var today = new Date();
                        // var date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
                        generateRequest("https://sepehrapi.irib.ir/v3/videonews?category_id=" +
                            vncid + "&page=1&page_size=" +
                            pageSize + "&include_details=true&include_media_resources=true&order_types=RELEASE_DATE_DESC", 'GET')
                            .then(
                                (vnsResult) => {
                                    this.setState({
                                        isLoaded: true,
                                        vnItem: vnResult.list[0],
                                        vnItems: vnsResult.list,
                                        catId: vncid
                                    });


                                },
                                (error) => {
                                    this.setState({
                                        isLoaded: true,
                                        error
                                    });
                                }
                            )

                    },
                    (error) => {
                        this.setState({
                            isLoaded: true,
                            error
                        });
                    }
                )
        }


    }

    componentWillReceiveProps(nextProps) {
        // console.log("componentWillReceiveProps")
        this.props = nextProps;
        $('#preloaderContainer').show();
        // this.state.isLoaded = false;
        this.fetchPageContent();

    }


    componentWillUnmount() {
        // if (!!this.player) {
        //     this.player.destroy();
        // }
    }

    componentDidUpdate(prevProps) {
    }

    componentDidMount() {
        $('#preloaderContainer').show();
        this.fetchPageContent();

        setTimeout(function () { //Start the timer

        }.bind(this), 3000)

    }



    formatDate(unix_timestamp) {
        var date = new Date(unix_timestamp);
        var hours = "0" + date.getHours();
        var minutes = "0" + date.getMinutes();
        var seconds = "0" + date.getSeconds();
        var formattedTime = hours.substr(-2) + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);
        return formattedTime;

    }

    htmlDecode(input) {
        var e = document.createElement('div');
        e.innerHTML = input;
        return e.childNodes.length === 0 ? "" : e.childNodes[0].nodeValue;
    }


    handlePlayer = (e) => {
        console.log("handlePlayer VNLIST")
        $("video").on("play", function () {
            $("video").not(this).each(function () {
                this.pause();
            });
            this[this.paused ? "pause" : "play"]();
        });
    };

    render() {
        const {error, isLoaded, vnItem, vnItems, catId} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {
                // if (true) {
                let vnItemsT = null;
                if (vnItem != null) {
                    let vnItemsTT = vnItems.filter(function (ele) {
                        return ele.id != vnItem.id;
                    });
                    vnItemsT = [vnItem].concat(vnItemsTT) // [ 4, 3, 2, 1 ]
                } else
                    vnItemsT = vnItems;
                let vnItemTCount = vnItemsT.length;
                const vnItemsHTML = [];
                if (vnItemTCount > 0) {
                    for (let i = 0; i < vnItemTCount; i++) {
                        let vnItemT = vnItemsT[i];
                        vnItemsHTML.push(
                            <VideoNewsPlayItem2 vnItem={vnItemT} catId={catId} handlePlayer={this.handlePlayer}/>
                        )
                    }
                } else {
                    vnItemsHTML.push(
                        <div className="alert unread custom-alert-3 alert-primary">
                            <div className="alert-text w-75">
                                <h6 className="text-truncate">
                                    بدون خبر
                                </h6>
                            </div>
                        </div>
                    )
                }
                $('#preloaderContainer').hide();

                return (
                    <div className="container pt-5">
                        {vnItemsHTML}
                    </div>
                );
            }


    }
}
