import React from "react";
import {NavLink} from "react-router-dom";
import CryptoJS from "crypto-js";

export class LoginMain extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            sItems: [],
            phoneNumber: null,
            isLogin: true
        };

    }

    getStructureItems() {
        this.setState({
            isLoaded: true,
        });

    }

    componentWillReceiveProps(nextProps) {

    }

    componentDidUpdate(prevProps) {

    }

    componentDidMount() {
        $('#preloaderContainer').show();
        this.getStructureItems();
    }

    sendOTP = () => {
        let phoneNumber = this.checkPhoneNumber($("#otpPhoneNumber").val());
        if (phoneNumber.length == 0) {
            $("#OTPMSG").text("شماره وارد شده صحیح نیست")
            $("#OTPMSG").addClass("text-danger")
        } else {
            this.state.phoneNumber = phoneNumber[0]
            fetch("https://sepehrapi.irib.ir/v3/getLogin", {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': 'OAuth oauth_consumer_key="84ALFkdjpBX0DSR3DsaLo364lKs1hTGq",oauth_token="b49255684ad9347386d890a04a642bfa7052d69ca568938b622ca7d84ed93972",oauth_signature_method="HMAC-SHA1",oauth_timestamp="1621247956",oauth_nonce="vlD9RSw7evg",oauth_version="1.0",oauth_signature="DITt4qzHS8j4QCi4Cs6vZBuXUW4%3D"',
                },
                body: JSON.stringify({phone_number: this.state.phoneNumber})
                // body: JSON.stringify(data),
            })
                .then(res => res.json())
                .then(
                    (otpResult) => {
                        $("#OTPMSG").removeClass("text-danger")
                        this.setState({
                            isLoaded: true,
                            isLogin: false
                        });
                    },
                    (error) => {
                        $("#OTPMSG").removeClass("text-danger")
                        this.setState({
                            isLoaded: true,
                            isLogin: false
                        });
                        // this.setState({
                        //     isLoaded: true,
                        //     error
                        // });
                    }
                )
        }

    }
    sendVerify = () => {
        //ToDo: KEEP CURRENT PAGE!!!
        let otpV = $("#otpV").val();
        fetch("https://sepehrapi.irib.ir/v3/setLogin", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'OAuth oauth_consumer_key="84ALFkdjpBX0DSR3DsaLo364lKs1hTGq",oauth_token="b49255684ad9347386d890a04a642bfa7052d69ca568938b622ca7d84ed93972",oauth_signature_method="HMAC-SHA1",oauth_timestamp="1621247956",oauth_nonce="vlD9RSw7evg",oauth_version="1.0",oauth_signature="DITt4qzHS8j4QCi4Cs6vZBuXUW4%3D"',
            },
            body: JSON.stringify({phone_number: this.state.phoneNumber, code: CryptoJS.MD5(otpV).toString()})
            // body: JSON.stringify(data),
        })
            .then(res => res.json())
            .then(
                (otpResult) => {
                    sessionStorage.setItem('token', otpResult.tokens[0].token);
                    sessionStorage.setItem('secret', otpResult.tokens[0].secret);
                    sessionStorage.setItem('phoneNumber', this.state.phoneNumber);

                    this.props.history.push('/')
                    this.setState({
                        isLoaded: true,
                        isLogin: false
                    });
                },
                (error) => {
                    $("#OTPVMSG").text("رمز وارد شده صحیح نیست")
                    $("#OTPVMSG").addClass("text-danger")

                    // console.log(error)
                    // this.setState({
                    //     isLoaded: true,
                    //     error
                    // });
                }
            )
    }
    _handleOTPKeyDown = (e) => {
        if (e.key === 'Enter') {
            this.sendOTP();
        }
    }
    _handleVKeyDown = (e) => {
        if (e.key === 'Enter') {
            this.sendVerify();
        }
    }

    checkPhoneNumber(str) {
        var
            mobileReg = /(0|\+98)?([ ]|-|[()]){0,2}9[1|2|3|4]([ ]|-|[()]){0,2}(?:[0-9]([ ]|-|[()]){0,2}){8}/ig,
            junkReg = /[^\d]/ig,
            persinNum = [/۰/gi, /۱/gi, /۲/gi, /۳/gi, /۴/gi, /۵/gi, /۶/gi, /۷/gi, /۸/gi, /۹/gi],
            num2en = function (str) {
                for (var i = 0; i < 10; i++) {
                    str = str.replace(persinNum[i], i);
                }
                return str;
            }

        var mobiles = num2en(str + '').match(mobileReg) || [];
        mobiles.forEach(function (value, index, arr) {
            arr[index] = value.replace(junkReg, '');
            arr[index][0] === '0' || (arr[index] = '0' + arr[index]);
        });
        return mobiles;
// test

    }

    render() {

        const {error, isLoaded, sItems, homeItems} = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (
            !isLoaded
        ) {
            return <div>در حال دریافت اطلاعات...</div>;
        } else {
            let html = "";
            let phoneNumber  = sessionStorage.getItem('phoneNumber');
            if(  phoneNumber != null
                || sessionStorage.getItem('token') != null
                || sessionStorage.getItem('secret') != null){
                html =
                    <div className="card-body px-5 text-center">
                        <img className="mb-4" src="/img/sepehr_login.png" alt="" height={150} width={150} />
                        <h4 className="mb-4"> شما با شماره ی
                            <strong className="ms-1" style={{textAlign: "left"}}> {phoneNumber} </strong>
                            وارد هستید.</h4>
                        <NavLink to={"/logout"} className="btn btn-creative btn-danger">خروج</NavLink>
                        <br/>
                        <br/>
                        <NavLink to={"/"} className="btn btn-creative btn-warning">بازگشت به خانه</NavLink>
                    </div>
            }
            else if (this.state.isLogin) {
                html =
                    <div>
                        <div className="text-center px-4">
                            <img className="mx-auto mb-4 d-block" src="/img/sepehr_login.png" height={150} width={150} alt=""/>
                            <h3>ورود با شماره همراه</h3>
                            <p className="mb-4" id="OTPMSG">رمزعبور به این شماره ارسال خواهد شد.</p>
                        </div>
                        <div className="otp-form mt-1 px-4">
                            <div className="input-group mb-3" style={{direction: "ltr"}}>
                                {/*<select className="input-group-text form-select" name="dialingcode"*/}
                                {/*        aria-label="Default select example">*/}
                                {/*    <option value="+98" selected>+98</option>*/}
                                {/*</select>*/}
                                <input
                                    id="otpPhoneNumber"
                                    className="form-control"
                                    type="text"
                                    // value={"09128178658"}
                                    style={{textAlign: "center"}}
                                    onKeyDown={this._handleOTPKeyDown}
                                    placeholder="شماره همراه"/>
                            </div>
                            <button className="btn btn-primary w-100" type="button"
                                // onClick={() => this.sendOTP($("otpPhoneNumber").val())}
                                    onClick={this.sendOTP}>ورود
                            </button>
                        </div>
                    </div>
            } else {
                // let phoneNumber = " 98" + this.state.phoneNumber + "+";
                let phoneNumber = " " + this.state.phoneNumber + " ";
                html =
                    <div>
                        <div className="text-center px-4">
                            <img className="mx-auto mb-4 d-block" src="/img/sepehr_login.png" height={150} width={150}  alt=""/>
                            <h3>تایید شماره همراه</h3>
                            <p className="mb-4" id="OTPVMSG"> رمزعبور به شماره ی
                                <strong className="ms-1" style={{textAlign: "left"}}> {phoneNumber} </strong>
                                ارسال شد. </p>
                        </div>
                        <div className="otp-verify-form mt-5 px-4">
                            <div className="input-group mb-3">
                                <input className="form-control"
                                       id="otpV"
                                       type="text"
                                       onKeyDown={this._handleVKeyDown}
                                       style={{textAlign: "center", direction: "ltr"}}
                                       placeholder="رمزعبور"/>
                                {/*<input className="form-control" type="text" value="" placeholder="-" maxLength={1}/>*/}
                                {/*<input className="form-control" type="text" value="" placeholder="-"/>*/}
                                {/*<input className="form-control" type="text" value="" placeholder="-"/>*/}
                                {/*<input className="form-control" type="text" value="" placeholder="-"/>*/}
                            </div>
                            <button className="btn btn-primary w-100" onClick={this.sendVerify}>تایید</button>
                        </div>
                        {/*<div className="login-meta-data px-4 text-center">*/}
                        {/*    <p className="mt-3 mb-0">Don't received the OTP?<span className="otp-sec ms-1"*/}
                        {/*                                                          id="resendOTP"></span></p>*/}
                        {/*</div>*/}
                    </div>
            }

            $('#preloaderContainer').hide();
            return (
                <div className="login-wrapper d-flex align-items-center justify-content-center">
                    <div className="container">
                        <div className="row justify-content-center">
                            <div className="col-12 col-sm-9 col-md-7 col-lg-6 col-xl-5">
                                <div className="card">
                                    <div className="card-body px-5 text-center">
                                        {html}
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            );
        }
    }

}